import {ClrDatagridComparatorInterface} from "@clr/angular";
import {IscsiTarget} from "@generated/iscsi-target";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";

export class TargetLocationComparator implements ClrDatagridComparatorInterface<IscsiTarget> {
   constructor(private preferredDomainName: string, private secondaryDomainName: string) {}

   compare = (t1: IscsiTarget, t2: IscsiTarget): number => {
      return VsanIscsiUtil.getTargetLocationLabel(t1.affinityLocation, this.preferredDomainName,
            this.secondaryDomainName).localeCompare(
                  VsanIscsiUtil.getTargetLocationLabel(t2.affinityLocation, this.preferredDomainName,
                        this.secondaryDomainName));
   };
}