import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {TargetLunRemoveSpec} from '@generated/target-lun-remove-spec';
import {VsanIscsiTargetMutationProvider} from "@generated/vsan-iscsi-target-mutation-provider";

@Component({
   templateUrl: "remove-luns.html",
   styleUrls: ["remove-luns.scss"],
   selector: "vsan-iscsi-remove-luns",
})
export class RemoveLunsComponent implements OnInit {
   isRemoving: boolean;
   alertMessage: string;
   dialogContent: string;
   targetAlias: string;
   lunIds: number[];

   constructor (private targetMutationProvider: VsanIscsiTargetMutationProvider) {}

   ngOnInit() {
      this.targetAlias = VsanUiUtils.getModalContext().targetAlias;
      this.lunIds = VsanUiUtils.getModalContext().lunIds;
      if (this.lunIds.length === 1) {
         this.dialogContent =
               VsanUiUtils.getString("vsan.configure.iscsiTargets.lun.remove.dialog.confirmation.singular");
      } else {
         this.dialogContent =
               VsanUiUtils.getString("vsan.configure.iscsiTargets.lun.remove.dialog.confirmation.plural",
                     this.lunIds.length);
      }
   }

   ok = (): void => {
      let removeSpec: TargetLunRemoveSpec = TargetLunRemoveSpec.Factory.create(this.targetAlias, this.lunIds);
      this.isRemoving = true;
      this.targetMutationProvider.removeLun(ManagedObject.contextObject, removeSpec)
            .then(this.close)
            .catch((error: string) => {
               this.isRemoving = false;
               this.alertMessage = error;
            });
   };

   close = (tasks: ManagedObject[] = null): void => {
      this.isRemoving = false;
      VsanUiUtils.closeModalDialog({
            "tasks": tasks,
            "refreshTargetList": true,
            "aliasOfChangedTarget": this.targetAlias});
   };
}