import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {VsanIscsiPropertyProvider} from "@generated/vsan-iscsi-property-provider";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {Logger} from "@util/logger";
import {VsanPropertyProvider} from "@generated/vsan-property-provider";
import {VsanIscsiTargetConfig} from "@generated/vsan-iscsi-target-config";
import {IscsiTarget} from "@generated/iscsi-target";

@Component({
   templateUrl: "iscsi-target-service.html",
   styleUrls: ["./iscsi-target-service.scss"],
   selector: "vsan-iscsi-targets",
})
export class IscsiTargetServiceComponent implements OnInit {
   iscsiViews: string[] = [
      VsanUiUtils.getString("vsan.configure.iscsiTargetService.targets"),
      VsanUiUtils.getString("vsan.configure.iscsiTargetService.initiator.groups")];

   currentViewIndex: number = 0;
   loading: boolean = true;
   hasEditPrivilege: boolean = false;
   readPoliciesPermission: boolean = false;
   alertMessage: string;

   // Stores the selected target in order to pass it to children which are recreated on refresh
   selectedIscsiTarget: IscsiTarget;

   constructor(private iscsiProvider: VsanIscsiPropertyProvider,
         private vsanService: VsanPropertyProvider,
         private permissionService: PermissionService) {
   }

   ngOnInit() {
      this.fetch();
   }

   @RefreshHandler
   fetch() {
      this.alertMessage = null;
      this.loading = true;
      Promise.all([
         this.iscsiProvider.getVsanIscsiTargetConfig(ManagedObject.contextObject),
         this.permissionService.hasPermissions(ManagedObject.contextObject, [Privileges.EDIT_CLUSTER()]),
         this.permissionService.hasVcPermissions(ManagedObject.contextObject, [Privileges.READ_POLICIES()]),
      ]).then(this.handleResponse).catch(this.handleError);
   }

   private handleResponse = (result: any[]) => {
      this.loading = false;

      const iscsiTargetConfig: VsanIscsiTargetConfig = result[0];
      this.readPoliciesPermission = result[2];
      this.hasEditPrivilege = result[1] && this.readPoliciesPermission && !iscsiTargetConfig.emptyCluster;
   };

   private handleError = (ex) => {
      this.loading = false;
      Logger.error(ex);
      this.alertMessage = VsanUiUtils.getString("vsan.common.error.dataExtraction");
   };

   handleConfigurationError = (event: any) => {
      Logger.error(event);
      this.alertMessage = event;
   };

   onViewSelected = (viewIndex: number): void => {
      if (viewIndex === this.currentViewIndex) {
         return;
      }
      this.currentViewIndex = viewIndex;
   };
}
