import {Component, OnInit} from "@angular/core";
import {AbstractControl, FormBuilder, FormControl, FormGroup, ValidationErrors, ValidatorFn,} from "@angular/forms";
import {InitiatorGroupAdditionSpec} from "@generated/initiator-group-addition-spec";
import {VsanIscsiInitiatorGroupMutationProvider} from "@generated/vsan-iscsi-initiator-group-mutation-provider";
import {ClusterComputeResource} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ManagedObject} from "@service/managed-object";
import {ReactiveFormUtil} from "@util/reactive-form-util";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "add-initiator-group.html",
   styleUrls: ["add-initiator-group.scss"],
   selector: "vsan-iscsi-add-initiator-group",
})

export class AddInitiatorGroupComponent implements OnInit {
   private static readonly initiatorGroupNameExp: RegExp = /^[a-zA-Z0-9:._-]+$/;
   private static readonly allDigitsExp: RegExp = /^[0-9]+$/;

   addInitiatorGroup: FormGroup;
   clusterName: string;
   groupNameErrorMessage: string;

   alertMessages: string[];
   isCreatingGroup: boolean = false;
   private existingGroupNames: string[] = [];
   private initiators: string[];

   constructor(private initiatorGroupMutationProvider: VsanIscsiInitiatorGroupMutationProvider,
         private dataSvc: DataServiceClient,
         private formBuilder: FormBuilder) {
      this.addInitiatorGroup = this.formBuilder.group({
         groupName: new FormControl("", [this.validate]),
      });
   }

   async ngOnInit(): Promise<void> {
      this.existingGroupNames = VsanUiUtils.getModalContext().groupNames;
      this.clusterName = await this.dataSvc.getProperty(ClusterComputeResource.name, ManagedObject.contextObject);
   }

   updateSelection(value: string[]): void {
      this.initiators = value;
   }

   get blockApplyButton(): boolean {
      return this.isCreatingGroup
            || (this.alertMessages && this.alertMessages.length > 0)
            || (this.addInitiatorGroup && this.addInitiatorGroup.touched && this.addInitiatorGroup.invalid);
   }

   ok = (): void => {
      this.alertMessages = null;

      if (!this.addInitiatorGroup.valid) {
         ReactiveFormUtil.validateAllFormFields(this.addInitiatorGroup);
         return;
      }

      const groupName: string = this.getGroupName();

      const createSpec: InitiatorGroupAdditionSpec = InitiatorGroupAdditionSpec.Factory.create(
            groupName, this.initiators);
      this.isCreatingGroup = true;
      this.initiatorGroupMutationProvider.createInitiatorGroup(ManagedObject.contextObject, createSpec)
            .then(() => {
               this.isCreatingGroup = false;
               VsanUiUtils.closeModalDialog(true);
            })
            .catch((error: string) => {
               this.isCreatingGroup = false;
               this.alertMessages = [error];
            });
   };

   private getGroupName(): string {
      return this.addInitiatorGroup.get("groupName").value.trim();
   }

   validate: ValidatorFn = (ctrl: AbstractControl): ValidationErrors | null => {
      this.groupNameErrorMessage = this.getGroupNameErrorMessage(ctrl.value);
      if (!this.groupNameErrorMessage) {
         return null;
      }

      return {'validationError': {value: this.groupNameErrorMessage}};
   };

   /**
    * Validate initiator group name and return the corresponding error message.
    *
    * Rules:
    * 1. Cannot be empty.
    * 2. Allowed characters are A-Z, a-z, 0-9, ".", ":", "-", "_"
    * 3. The name cannot start with "iqn." or "eui."
    * 4. Cannot be all digits.
    * 5. Cannot be repeated with an existing name.
    */
   private getGroupNameErrorMessage = (name: string): string => {
      if (!name) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.initiatorGroups.group.name.isNull");
      }
      if (!AddInitiatorGroupComponent.initiatorGroupNameExp.test(name)) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.initiatorGroups.group.name.invalidCharacters");
      }
      if (name.indexOf("iqn.") === 0 || name.indexOf("eui.") === 0) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.initiatorGroups.group.name.invalidPrefix");
      }
      if (AddInitiatorGroupComponent.allDigitsExp.test(name)) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.initiatorGroups.group.name.allDigits");
      }
      if (this.existingGroupNames.indexOf(name) > -1) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.initiatorGroups.group.name.alreadyExists");
      }
      return null;
   };
}
