import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {Icon} from "@util/icon";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {ModalBuilder} from "@util/modal-builder";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";
import {VsanObject} from "@generated/vsan-object";
import {IscsiTarget} from "@generated/iscsi-target";
import {NetworkIpSetting} from "@generated/network-ip-setting";
import {TargetOperationSpec} from "@generated/target-operation-spec";
import {IscsiContext} from "@navigation/context/iscsi-context";
import {VsanIscsiTargetPropertyProvider} from "@generated/vsan-iscsi-target-property-provider";
import {VsanIscsiTargetMutationProvider} from "@generated/vsan-iscsi-target-mutation-provider";
import {NetworkIpConfigProvider} from "@generated/network-ip-config-provider";
import {Logger} from "@util/logger";
// Import filters
import {IqnFilter} from "../filters/iqn-filter";
import {AliasFilter} from "../filters/alias-filter";
import {LunCountFilter} from "../filters/lun-count-filter";
import {NetworkInterfaceFilter} from "../filters/network-interface-filter";
import {IoOwnerHostFilter} from "../filters/io-owner-host-filter";
import {PortFilter} from "../filters/port-filter";
import {HealthFilter} from "../filters/health-filter";
import {StoragePolicyFilter} from "../filters/storage-policy-filter";
import {ComplianceResultFilter} from "../filters/compliance-result-filter";
import {AuthTypeFilter} from "../filters/auth-type-filter";
import {TargetLocationFilter} from "../filters/target-location-filter";
import {ClrDatagridSortOrder} from "@clr/angular";
// Import comparators
import {IoOwnerHostComparator} from "../comparators/io-owner-host-comparator";
import {HealthComparator} from "../comparators/health-comparator";
import {ComplianceResultComparator} from "../comparators/compliance-result-comparator";
import {AuthTypeComparator} from "../comparators/auth-type-comparator";
import {TargetLocationComparator} from "../comparators/target-location-comparator";
import {TaskMonitorService} from "@service/task-monitor-service";
import {TargetListData} from "@generated/target-list-data";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {AffinitySiteLocation} from "@generated/affinity-site-location";

@Component({
   selector: "vsan-iscsi-targets-view",
   templateUrl: "iscsi-targets-view.html",
   styleUrls: ["iscsi-targets-view.scss", "iscsi-common.scss"],
})
export class TargetsViewComponent implements OnInit {
   Icon: typeof Icon = Icon;
   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;
   VsanIscsiUtil: typeof VsanIscsiUtil = VsanIscsiUtil;

   loading: boolean = false;
   alertMessage: string;

   @Input()
   selectedTarget: IscsiTarget;

   @Output()
   selectedTargetChange: EventEmitter<IscsiTarget> = new EventEmitter<IscsiTarget>();

   @Input()
   hasComputePrivileges: boolean = false;

   @Input()
   readPoliciesPermission: boolean = false;
   // cache the ipSettings
   ipConfigDataMap: Map<string, NetworkIpSetting> = new Map<string, NetworkIpSetting>();

   ascSort: ClrDatagridSortOrder = ClrDatagridSortOrder.ASC;

   isTargetLocationSupported: boolean;
   private clusterRef: ManagedObject;
   private hostUidToHostIpMap: Map<string, string> = new Map<string, string>();
   private preferredDomainName: string;
   private secondaryDomainName: string;
   private preferredDomainHosts: string[] = [];
   private secondaryDomainHosts: string[] = [];

   private _targetList: IscsiTarget[];

   set targetList(list: IscsiTarget[]) {
      if (!list || list.length === 0) {
         this._targetList = [];
      } else {
         this._targetList = list;
      }
   }

   get targetList(): IscsiTarget[] {
      return this._targetList;
   }

   // iSCSI target list filter
   iqnFilter: IqnFilter = new IqnFilter();
   aliasFilter: AliasFilter = new AliasFilter();
   lunCountFilter: LunCountFilter = new LunCountFilter();
   networkInterfaceFilter: NetworkInterfaceFilter = new NetworkInterfaceFilter();
   ioOwnerHostFilter: IoOwnerHostFilter = new IoOwnerHostFilter(this.hostUidToHostIpMap);
   portFilter: PortFilter = new PortFilter();
   healthFilter: HealthFilter = new HealthFilter();
   storagePolicyFilter: StoragePolicyFilter = new StoragePolicyFilter();
   complianceResultFilter: ComplianceResultFilter = new ComplianceResultFilter();
   authTypeFilter: AuthTypeFilter = new AuthTypeFilter();
   targetLocationFilter: TargetLocationFilter;

   // iSCSI target list comparator
   ioOwnerHostComparator: IoOwnerHostComparator = new IoOwnerHostComparator(this.hostUidToHostIpMap);
   healthComparator: HealthComparator = new HealthComparator();
   complianceResultComparator: ComplianceResultComparator = new ComplianceResultComparator();
   authTypeComparator: AuthTypeComparator = new AuthTypeComparator();
   targetLocationComparator: TargetLocationComparator;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   constructor(private iscsiTargetProvider: VsanIscsiTargetPropertyProvider,
         private targetMutationProvider: VsanIscsiTargetMutationProvider,
         private ipDataProvider: NetworkIpConfigProvider,
         private taskSvc: TaskMonitorService) {
   }

   ngOnInit(): void {
      this.clusterRef = ManagedObject.contextObject;
      this.fetch();
   }

   fetch(): void {
      this.loading = true;
      this.iscsiTargetProvider.getTargetListData(this.clusterRef).then((data: TargetListData) => {
         this.loading = false;
         this.targetList = data.targets || [];

         Object.keys(data.hostUidToHostIpMap).map(hostUid => {
            this.hostUidToHostIpMap.set(hostUid, data.hostUidToHostIpMap[hostUid]);
         });

         this.isTargetLocationSupported = data.isTargetLocationSupported;
         if (this.isTargetLocationSupported) {
            this.preferredDomainName = data.preferredDomainName;
            this.secondaryDomainName = data.secondaryDomainName;
            this.preferredDomainHosts = data.preferredDomainHostUids;
            this.secondaryDomainHosts = data.secondaryDomainHostUids;

            this.targetLocationFilter = new TargetLocationFilter(this.preferredDomainName, this.secondaryDomainName);
            this.targetLocationComparator =
                  new TargetLocationComparator(this.preferredDomainName, this.secondaryDomainName);
         }

         if (this.targetList.length) {
            this.queryVnicIpAddress();

            const context: IscsiContext = VsanUiUtils.getNavigationData();
            if (context && context.targetAlias) {
               // Target is passed from context, select it
               this.selectTargetByName(context.targetAlias);
            } else if (this.selectedTarget && this.selectedTarget.alias) {
               // Update selected target reference on refresh
               this.selectTargetByName(this.selectedTarget.alias);
            } else {
               // Select first target on initial load
               this.selectTarget(this.targetList[0]);
            }
         } else {
            this.selectTarget(null);
         }

         this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded.andTabSelected",
               VsanUiUtils.getString("vsan.iscsiTargets"),
               VsanUiUtils.getString("vsan.configure.iscsiTargetService.targets"));
      }).catch((error: string) => {
         this.loading = false;
         this.targetList = [];
         this.alertMessage = VsanUiUtils.getString("vsan.common.error.dataExtraction");
         Logger.error("Unable to fetch data: " + error);
      });
   }

   private queryVnicIpAddress = () => {
      const hostRefs: ManagedObject[] = [];
      const vnicNames: string[] = [];
      const targetsOnConnectedHosts: IscsiTarget[] =
            this.targetList.filter(target => this.isValidHost(target.ioOwnerHost));
      if (targetsOnConnectedHosts.length === 0) {
         return;
      }

      targetsOnConnectedHosts.forEach((target) => {
         const ioOwnerHost: string = target.ioOwnerHost;
         hostRefs.push(ManagedObject.fromUid(ioOwnerHost));
         vnicNames.push(target.networkInterface);
      });

      this.ipDataProvider.getIpSetting(hostRefs, vnicNames).then((networkIpSettings: NetworkIpSetting[]) => {
         if (networkIpSettings.length > 0) {
            networkIpSettings.forEach((setting, index) => {
               const key: string = this.getTargetNetworkKey(targetsOnConnectedHosts[index]);
               this.ipConfigDataMap.set(key, setting);
            });
         }
      }).catch((error) => {
         this.alertMessage = VsanUiUtils.getString("vsan.common.error.dataExtraction");
         Logger.error("Unable to query the network ip settings: " + error);
      });
   };

   selectTargetByName = (targetAlias: string) => {
      if (!this.targetList || !this.targetList.length) {
         return;
      }
      // The previously selectedTarget might be removed from the view, so check if it is still in the targets list.
      const target: IscsiTarget = this.targetList.find(currentTarget => currentTarget.alias === targetAlias);
      this.selectTarget(target ? target : this.targetList[0]);
   };

   selectTarget = (target: IscsiTarget) => {
      this.selectedTarget = target;
      this.selectedTargetChange.emit(this.selectedTarget);
   };

   getIoOwnerHostUuid(target: IscsiTarget): string {
      return this.isValidHost(target.ioOwnerHost) ? target.ioOwnerHost : null;
   }

   getIoOwnerHostName(target: IscsiTarget): string {
      const uuid: string = this.getIoOwnerHostUuid(target);
      return this.hostUidToHostIpMap.get(uuid);
   }

   openAddTargetDialog = () => {
      this.openTargetModificationDialog(true);
   };

   openEditTargetDialog = () => {
      this.openTargetModificationDialog(false);
   };

   private openTargetModificationDialog = (addTarget: boolean) => {
      let target: IscsiTarget = null;
      if (addTarget) {
         // Open add target dialog.
      } else {
         // Open edit target dialog.
         target = this.selectedTarget;
      }
      new ModalBuilder("iscsiTargets/targetModification")
            .setSize(800, 400)
            .open(target)
            .then(this.updateTargetList);
   };

   get reapplyActionEnabled(): boolean {
      return this.hasComputePrivileges && this.hasMismatchComplianceResult(this.selectedTarget);
   }

   get reapplyActionTitle(): string {
      if (!this.hasComputePrivileges) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      } else if (!this.hasMismatchComplianceResult(this.selectedTarget)) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.target.reapply.disabled.description");
      } else {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.target.reapply.description");
      }
   }

   get batchReapplyActionEnabled(): boolean {
      return this.hasComputePrivileges && this.targetList.some(target => this.hasMismatchComplianceResult(target));
   }

   get batchReapplyActionTitle(): string {
      if (!this.hasComputePrivileges) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      if (!this.targetList.some(target => this.hasMismatchComplianceResult(target))) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.target.batch.reapply.disabled.description");
      }
      return VsanUiUtils.getString("vsan.configure.iscsiTargets.target.batch.reapply.description");
   }

   /**
    * Reapply storage policy to the iSCSI target.
    */
   reapplyPolicy = () => {
      const targetSpec: TargetOperationSpec = TargetOperationSpec.Factory.create(this.selectedTarget.alias, null, null,
            null, null, null, this.selectedTarget.vmStoragePolicyUuid, null);
      this.targetMutationProvider.editTarget(this.clusterRef, targetSpec).then(this.updateTargetList)
            .catch((error: string) => {
               this.alertMessage = VsanUiUtils.getString("vsan.configure.iscsiTargets.target.reapply.error");
            });
   };

   /**
    * Reapply storage policy to the iSCSI targets by batch.
    */
   batchReapplyPolicies = () => {
      const mismatchTargets: IscsiTarget[] = this.targetList.filter(target => this.hasMismatchComplianceResult(target));
      new ModalBuilder("iscsiTargets/batchPolicyReapply")
            .setSize(700, 388)
            .open({
               targets: mismatchTargets,
            })
            .then(this.updateTargetList);
   };

   private hasMismatchComplianceResult = (target: any): boolean => {
      const targetObject: VsanObject = target as VsanObject;
      return targetObject && targetObject.storagePolicy && targetObject.complianceResult &&
            targetObject.complianceResult.mismatch;
   };

   openRemoveTargetDialog = () => {
      new ModalBuilder("iscsiTargets/removeTarget")
            .setSize(500, 190)
            .open({
               targetAlias: this.selectedTarget.alias,
               lunCount: this.selectedTarget.lunCount,
            })
            .then(this.updateTargetList);
   };

   updateTargetList = (tasks: ManagedObject | ManagedObject[]) => {
      if (!tasks) {
         return;
      }
      if (Array.isArray(tasks)) {
         if (tasks.length === 0) {
            return;
         }
      } else {
         tasks = [tasks];
      }
      const promises: Promise<any>[] = tasks.map(t => this.taskSvc.getAwaitResult(t));
      Promise.all(promises).then(() => this.fetch()).catch((error: string) => {
         this.alertMessage = error;
      });
   };

   handleDataFailed = (error: string) => {
      this.alertMessage = error;
   };

   /**
    * Update the LUNs of specified target after adding or removing luns.
    */
   updateTarget = (aliasOfChangedTarget: string) => {
      this.iscsiTargetProvider.getIscsiTarget(this.clusterRef, aliasOfChangedTarget)
            .then((refreshedTarget: IscsiTarget) => {
               this.targetList.forEach((target: IscsiTarget) => {
                  if (target.iqn === refreshedTarget.iqn) {
                     target.lunCount = refreshedTarget.lunCount;
                  }
               });
            })
            .catch((error: string) => {
               this.alertMessage = VsanUiUtils.getString("vsan.common.error.dataExtraction");
               Logger.error("Unable to query a specific iSCSI target with name " + aliasOfChangedTarget + ": " + error);
            });
   };

   complianceResultVisible = (target: any): boolean => {
      return target.storagePolicy && target.complianceResult;
   };

   getDomainName(ioOwnerHost: string): string {
      if (this.preferredDomainHosts.indexOf(ioOwnerHost) > -1) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.target.list.io.owner.preferred");
      }
      if (this.secondaryDomainHosts.indexOf(ioOwnerHost) > -1) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.target.list.io.owner.host.secondary");
      }
      return null;
   }

   isIoOwnerHostInDesiredDomain(target: IscsiTarget): boolean {
      switch (target.affinityLocation) {
         case AffinitySiteLocation.PREFERRED_DOMAIN:
            return this.preferredDomainHosts.indexOf(target.ioOwnerHost) > -1;
         case AffinitySiteLocation.NON_PREFERRED_DOMAIN:
            return this.secondaryDomainHosts.indexOf(target.ioOwnerHost) > -1;
         default:
            return false;
      }
   }

   getTargetLocationShape(target: IscsiTarget): Icon {
      if (target.affinityLocation === AffinitySiteLocation.NONE) {
         return Icon.CLR_INFO_STANDARD_ICON_SHAPE;
      }
      return this.isIoOwnerHostInDesiredDomain(target) ?
            Icon.CLR_SUCCESS_STANDARD_ICON_SHAPE : Icon.CLR_WARN_STANDARD_ICON_SHAPE;
   }

   getTargetLocationLabel(location: AffinitySiteLocation): string {
      return VsanIscsiUtil.getTargetLocationLabel(location, this.preferredDomainName, this.secondaryDomainName);
   }

   getTargetLocationIconClass(target: IscsiTarget): Icon {
      if (target.affinityLocation === AffinitySiteLocation.NONE) {
         return null;
      }
      return this.isIoOwnerHostInDesiredDomain(target) ?
            Icon.clrIconShapeClassMap[Icon.CLR_SUCCESS_STANDARD_ICON_SHAPE] :
            Icon.clrIconShapeClassMap[Icon.CLR_WARN_STANDARD_ICON_SHAPE];
   }

   getTargetLocationTooltip(location: AffinitySiteLocation): string {
      if (location === AffinitySiteLocation.NONE) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.target.list.location.tooltip.either");
      }
      return VsanUiUtils.getString("vsan.configure.iscsiTargets.target.list.location.tooltip.mismatch");
   }

   signpostClicked = (event: MouseEvent) => {
      /**
       * Stops the event bubble so checking the signpost content won't change the dagrid selection
       */
      if (event) {
         event.cancelBubble = true;
         event.stopPropagation();
         event.stopImmediatePropagation();
      }
   };

   /**
    * Host can have several vnics, so we should use host and the vnic as the key
    */
   getTargetNetworkKey = (target: IscsiTarget): string => {
      return target.ioOwnerHost + ":" + target.networkInterface;
   };

   isValidHost = (ioOwnerHost: string): boolean => {
      /**
       * ioOwnerHost is uid of a ManagedObject,
       * for example "urn:vmomi:HostSystem:host-15:10c24de3-e917-4a6a-abcd-7a03bf226f0c"
       * if a host is disconnected, the ioOwnerHost is a uuid, for example "10c24de3-e917-4a6a-abcd-7a03bf226f0c"
       * in this case this is not a valid host because we cannot get the detailed host information
       */
      if (ioOwnerHost) {
         return ioOwnerHost.split(":").length === 5;
      }
      return false;
   };
}
