import {VsanObject} from "@generated/vsan-object";
import {VsanIscsiUtil} from "../../common/util/vsan-iscsi-util";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

export class ComplianceResultFilter implements ClrDatagridStringFilterInterface<VsanObject> {
   accepts = (obj: VsanObject, search: string): boolean => {
      return VsanIscsiUtil.getComplianceLabel(obj.complianceResult).toLowerCase().indexOf(search) > -1;
   };
}