import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {VsanIscsiTargetMutationProvider} from "@generated/vsan-iscsi-target-mutation-provider";
import {TargetInitiatorSpec} from "@generated/target-initiator-spec";
import {TargetInitiatorRemoveSpec} from "@generated/target-initiator-remove-spec";

@Component({
   templateUrl: "remove-target-initiator.html",
   selector: "vsan-iscsi-remove-allowed-initiator",
})
export class RemoveTargetInitiatorComponent implements OnInit {
   initiator: TargetInitiatorSpec;
   targetAlias: string;

   alertMessages: string[] = null;
   buzy: boolean = false;

   constructor(private mutationProvider: VsanIscsiTargetMutationProvider) {}

   ngOnInit(): void {
      this.initiator = VsanUiUtils.getModalContext().initiator;
      this.targetAlias = VsanUiUtils.getModalContext().targetAlias;
   }

   apply = (): void => {
      let clusterRef: ManagedObject = ManagedObject.contextObject;
      let removeSpec: TargetInitiatorRemoveSpec =
            TargetInitiatorRemoveSpec.Factory.create(this.targetAlias, [this.initiator.name]);
      this.buzy = true;
      this.mutationProvider.disallowInitiatorAccess(clusterRef, removeSpec)
            .then(() => VsanUiUtils.closeModalDialog(true))
            .catch(this.handleError);
   }

   private handleError = (error: any) => {
      this.buzy = false;
      this.alertMessages = [error];
   }
}