import {Component, OnInit, ViewChild} from "@angular/core";
import {
   AbstractControl,
   FormBuilder,
   FormControl,
   FormGroup,
   ValidationErrors,
   ValidatorFn,
   Validators,
} from "@angular/forms";
import {ClrForm} from "@clr/angular";
import {IscsiLunStatus} from "@generated/iscsi-lun-status";
import {LunOperationSpec} from "@generated/lun-operation-spec";
import {PbmDataProvider} from "@generated/pbm-data-provider";
import {StoragePolicyData} from "@generated/storage-policy-data";
import {VsanIscsiTargetMutationProvider} from "@generated/vsan-iscsi-target-mutation-provider";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {StorageSizeMetric} from "@util/storage-size-metric";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "add-lun.html",
   styleUrls: ["lun-operation-dialog.scss"],
   selector: "vsan-iscsi-add-lun",
})
export class AddLunComponent implements OnInit {

   @LoadingIndicator()
   loading: boolean;

   @ErrorMessage()
   errorMessage: string;

   targetAlias: string;
   availableUnits: StorageSizeMetric[] = [StorageSizeMetric.MB, StorageSizeMetric.GB, StorageSizeMetric.TB];
   availablePolicies: StoragePolicyData[];
   iscsiLun: FormGroup;
   idError: string;
   sizeError: string;
   aliasError: string;
   policyError: string;

   private suggestedLunId: number;
   private clusterRef: ManagedObject;

   @ViewChild(ClrForm, {static: true})
   lunSettingsForm: ClrForm;

   constructor(private formBuilder: FormBuilder,
         private pbmProvider: PbmDataProvider,
         private targetMutationProvider: VsanIscsiTargetMutationProvider) {
      this.iscsiLun = this.formBuilder.group({
         id: new FormControl(null, [Validators.required, this.idValidator]),
         alias: new FormControl("", [this.aliasValidator]),
         policyUuid: new FormControl("", [Validators.required, this.policyValidator]),
         sizeAndUnits: this.formBuilder.group({
            size: new FormControl(null, Validators.required),
            sizeUnit: new FormControl(StorageSizeMetric.GB),
         }, {validators: this.sizeValidator}),
      });
   }

   @Loader()
   async ngOnInit(): Promise<void> {
      this.clusterRef = ManagedObject.contextObject;
      this.targetAlias = VsanUiUtils.getModalContext().targetAlias;
      this.suggestedLunId = VsanUiUtils.getModalContext().suggestedLunId;
      this.availablePolicies = await this.pbmProvider.getStoragePolicies(this.clusterRef);
      this.fillOutForm();
   }

   private fillOutForm = () => {
      const defaultStoragePolicyUuid: string = this.availablePolicies?.find(policy => policy.isDefault)?.id;

      this.iscsiLun.patchValue({
         id: this.suggestedLunId,
         policyUuid: defaultStoragePolicyUuid,
      });
   };

   @Loader()
   async ok(): Promise<void> {
      if (this.iscsiLun.invalid) {
         // Verify inputs when user opens the dialog and click Add button directly.
         this.lunSettingsForm.markAsTouched();
         return;
      }

      const addSpec: LunOperationSpec = LunOperationSpec.Factory.create(
            this.targetAlias,
            this.getAlias(),
            this.getId(),
            this.getSize().value * this.getSizeUnit().value.value,
            VsanIscsiUtil.UNCHANGED_NEW_LUN_ID_VALUE,
            IscsiLunStatus.Online.toString(),
            this.getPolicyUuid());

      const task: ManagedObject = await this.targetMutationProvider.createLun(this.clusterRef, addSpec);
      this.close(task);
   }

   close = (task: ManagedObject = null) => {
      VsanUiUtils.closeModalDialog({
         "tasks": [task],
         "refreshTargetList": true,
         "aliasOfChangedTarget": this.targetAlias,
      });
   };

   get blockApplyButton(): boolean {
      return this.loading
            || !!this.errorMessage
            || (this.iscsiLun && this.iscsiLun.touched && this.iscsiLun.invalid);
   }

   get sizeHelperMessage(): string {
      return VsanIscsiUtil.getLunSizeHelperMessage(this.getSizeUnit().value);
   }

   private getId(): number {
      return this.iscsiLun && this.iscsiLun.get("id").value;
   }

   private getAlias(): string {
      return this.iscsiLun && this.iscsiLun.get("alias").value;
   }

   private getPolicyUuid(): string {
      return this.iscsiLun && this.iscsiLun.get("policyUuid").value;
   }

   private getSize(): AbstractControl {
      return this.iscsiLun?.get("sizeAndUnits")?.get("size");
   }

   private getSizeUnit(): AbstractControl {
      return this.iscsiLun?.get("sizeAndUnits")?.get("sizeUnit");
   }

   private idValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      if (!this.iscsiLun) {
         return null;
      }

      this.idError = VsanIscsiUtil.getLunIdValidationMessage(control.value);
      if (this.idError) {
         return {"idInvalid": {value: control.value}};
      }
      return null;
   };

   private aliasValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      if (!this.iscsiLun) {
         return null;
      }

      // LUN alias can be empty.
      if (control.value) {
         this.aliasError = VsanIscsiUtil.getAliasValidationMessage(control.value);
         if (this.aliasError) {
            return {"aliasInvalid": {value: control.value}};
         }
      }

      return null;
   };

   private policyValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      if (!this.iscsiLun || !this.availablePolicies || this.availablePolicies.length === 0) {
         return null;
      }

      const selectedPolicy: StoragePolicyData = this.availablePolicies.find(policy => {
         return policy.id === control.value;
      });
      if (selectedPolicy) {
         this.policyError = VsanIscsiUtil.getLunPolicyValidationMessage(selectedPolicy);
         if (this.policyError) {
            return {"policyInvalid": {value: control.value}};
         }
      }
   };

   private sizeValidator: ValidatorFn = (): ValidationErrors | null => {
      if (!this.iscsiLun) {
         return null;
      }

      const size: AbstractControl = this.getSize();
      const sizeUnit: AbstractControl = this.getSizeUnit();
      this.sizeError = VsanIscsiUtil.getLunSizeValidationMessage(size.value, sizeUnit.value.value);
      if (this.sizeError) {
         const invalidSizeError: ValidationErrors = {
            "sizeInvalid": {value: size.value},
         };

         // Set error to the size form control in order to show the error with <clr-control-error> element
         size.setErrors(invalidSizeError);
         return invalidSizeError;
      } else {
         // Remove error from size form control to hide the <clr-control-error> element
         size.setErrors(null);
         return null;
      }
   };
}
