import {Component, OnInit, ViewChild} from "@angular/core";
import {ClrForm} from "@clr/angular";
import {
   AbstractControl,
   FormBuilder,
   FormControl,
   FormGroup,
   ValidationErrors,
   ValidatorFn,
   Validators,
} from "@angular/forms";
import {IscsiTarget} from "@generated/iscsi-target";
import {VsanIscsiTargetMutationProvider} from "@generated/vsan-iscsi-target-mutation-provider";
import {StoragePolicyData} from "@generated/storage-policy-data";
import {TargetOperationSpec} from "@generated/target-operation-spec";
import {VsanIscsiAuthSpec} from "@generated/vsan-iscsi-auth-spec";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";
import {VsanIscsiTargetConfig} from "@generated/vsan-iscsi-target-config";
import IscsiTargetAuthType = VsanIscsiTargetConfig.IscsiTargetAuthType;
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {TargetLocation} from "../model/target-location.data";
import {VsanIscsiTargetPropertyProvider} from "@generated/vsan-iscsi-target-property-provider";
import {TargetModificationData} from "@generated/target-modification-data";
import {AffinitySiteLocation} from "@generated/affinity-site-location";

/**
 * The component is used to add/edit an iscsi target object.
 */
@Component({
   selector: "vsan-target-modification",
   templateUrl: "target-modification.html",
   styleUrls: ["target-modification.scss"],
})
export class TargetModificationComponent implements OnInit {
   VsanIscsiUtil: typeof VsanIscsiUtil = VsanIscsiUtil;

   readonly IQN_CONTROL_NAME: string = "iqn";
   readonly PORT_CONTROL_NAME: string = "port";
   readonly ALIAS_CONTROL_NAME: string = "alias";
   readonly POLICY_CONTROL_NAME: string = "policy";
   readonly NETWORK_CONTROL_NAME: string = "network";
   readonly LOCATION_CONTROL_NAME: string = "location";
   readonly AUTH_SPEC_CONTROL_NAME: string = "authSpec";
   readonly ALIAS_VALIDATION: string = "aliasInvalid";
   private readonly IQN_VALIDATION: string = "iqnInvalid";
   private readonly POLICY_VALIDATION: string = "policyInvalid";

   title: string;

   private defaultHeight: number = 400;
   private chapHeight: number = 580;
   private chapMutualHeight: number = 680;
   private iqnConventionHeight: number = 45;
   private locationHeight: number = 75;

   private clusterRef: ManagedObject;
   loading: boolean = false;
   originalTarget: IscsiTarget = null;

   isTargetLocationSupported: boolean = false;
   private domains: DomainOrHostData[];

   locationAlert: string;
   errorMessages: string[];
   policyErrorMessage: string;
   aliasErrorMessage: string;

   private iscsiTargetConfig: VsanIscsiTargetConfig = null;
   availableNetworks: string[];
   availablePolicies: StoragePolicyData[];
   locations: TargetLocation[] = [];

   iscsiTarget: FormGroup;

   @ViewChild(ClrForm, {static: true})
   iscsiSettingsForm: ClrForm;

   constructor(private targetMutationProvider: VsanIscsiTargetMutationProvider,
         private iscsiTargetPropertyProvider: VsanIscsiTargetPropertyProvider,
         private formBuilder: FormBuilder) {
      this.iscsiTarget = this.formBuilder.group({
         [this.IQN_CONTROL_NAME]: new FormControl("", [this.iqnValidator]),
         [this.ALIAS_CONTROL_NAME]: new FormControl("", [Validators.required, this.aliasValidator]),
         [this.POLICY_CONTROL_NAME]: new FormControl("", [this.policyValidator]),
         [this.NETWORK_CONTROL_NAME]: new FormControl("", [Validators.required]),
         [this.PORT_CONTROL_NAME]: new FormControl({ values: VsanIscsiUtil.DEFAULT_PORT}, [
            Validators.required,
            Validators.min(VsanIscsiUtil.TCP_PORT_MIN),
            Validators.max(VsanIscsiUtil.TCP_PORT_MAX)]),
         [this.AUTH_SPEC_CONTROL_NAME]: new FormControl(""),
      });
   }

   async ngOnInit(): Promise<void> {
      this.clusterRef = ManagedObject.contextObject;
      this.originalTarget = VsanUiUtils.getModalContext();

      if (this.originalTarget) {
         this.title = VsanUiUtils.getString("vsan.configure.iscsiTargets.target.dialog.edit.title");
      } else {
         this.title = VsanUiUtils.getString("vsan.configure.iscsiTargets.target.dialog.add.title");
      }

      this.loading = true;
      this.iscsiTargetPropertyProvider.getTargetModificationData(this.clusterRef, !this.originalTarget)
            .then((data: TargetModificationData) => {
               this.loading = false;

               this.domains = data.domains;
               this.availablePolicies = data.policies;
               this.availableNetworks = data.networks;
               this.iscsiTargetConfig = data.iscsiTargetConfig;
               this.isTargetLocationSupported = data.isTargetLocationSupported;

               this.updateDialogHeights();

               if (this.isTargetLocationSupported) {
                  this.setLocations();
               }

               if (this.originalTarget) {
                  this.fillFormWithExistingData(this.originalTarget);
               } else {
                  this.fillFormDefaults();
               }
               this.onPolicyChange();

            }).catch(e => {
               this.handleError(e);
            });
   }

   private setLocations = () => {
      let preferredDomainName: string;
      let secondaryDomainName: string;
      this.domains
         .filter(domain => !domain.isHost)
         .forEach(domain => {
         if (domain.isPreferred) {
            preferredDomainName = domain.label;
         } else {
            secondaryDomainName = domain.label;
         }
      });

      this.locations = [
         new TargetLocation(
            AffinitySiteLocation.NONE,
            VsanIscsiUtil.getTargetLocationLabel(AffinitySiteLocation.NONE),
            true),
         new TargetLocation(AffinitySiteLocation.PREFERRED_DOMAIN,
            VsanIscsiUtil.getTargetLocationLabel(AffinitySiteLocation.PREFERRED_DOMAIN, preferredDomainName),
            true),
         new TargetLocation(AffinitySiteLocation.NON_PREFERRED_DOMAIN,
            VsanIscsiUtil.getTargetLocationLabel(AffinitySiteLocation.NON_PREFERRED_DOMAIN, null, secondaryDomainName),
            true,
      )];
   };

   /**
    * When the user adds a target, fill in the form using the default data.
    */
   private fillFormDefaults = (): void => {
      let port: number;
      let network: string;
      let authSpec: VsanIscsiAuthSpec;

      if (this.iscsiTargetConfig) {
         port = this.iscsiTargetConfig.port;
         network = this.iscsiTargetConfig.network;
         authSpec = VsanIscsiAuthSpec.Factory.create(
               this.iscsiTargetConfig.authType ?
                     this.iscsiTargetConfig.authType.toString() : IscsiTargetAuthType.NoAuth.toString(),
               this.iscsiTargetConfig.outgoingUser,
               this.iscsiTargetConfig.outgoingSecret,
               this.iscsiTargetConfig.incomingUser,
               this.iscsiTargetConfig.incomingSecret);
      } else {
         port = VsanIscsiUtil.DEFAULT_PORT;
         if (this.availableNetworks && this.availableNetworks.length > 0) {
            network = this.availableNetworks[0];
         }
         authSpec = VsanIscsiAuthSpec.Factory.create(IscsiTargetAuthType.NoAuth.toString(), null, null, null,
               null);
      }

      this.iscsiTarget.patchValue({
         [this.PORT_CONTROL_NAME]: port,
         [this.NETWORK_CONTROL_NAME]: network,
         [this.AUTH_SPEC_CONTROL_NAME]: authSpec,
         [this.POLICY_CONTROL_NAME]: this.getDefaultPolicy(),
      });
      if (this.isTargetLocationSupported) {
         this.iscsiTarget.addControl(this.LOCATION_CONTROL_NAME, new FormControl(AffinitySiteLocation.NONE));
      }
   };

   /**
    * When the users edits a target, fill in the form using original target data.
    */
   private fillFormWithExistingData = (target: IscsiTarget): void => {
      this.iscsiTarget.patchValue({
         [this.IQN_CONTROL_NAME]: target.iqn,
         [this.ALIAS_CONTROL_NAME]: target.alias,
         [this.PORT_CONTROL_NAME]: target.port,
         [this.NETWORK_CONTROL_NAME]: target.networkInterface,
         [this.POLICY_CONTROL_NAME]: this.getPolicyByUuid(target.vmStoragePolicyUuid),
         [this.AUTH_SPEC_CONTROL_NAME]: target.authSpec,
      });
      if (this.isTargetLocationSupported) {
         this.iscsiTarget.addControl(this.LOCATION_CONTROL_NAME, new FormControl(target.affinityLocation));
      }
   };

   onPolicyChange = () => {
      if (!this.isTargetLocationSupported) {
         return;
      }

      // Only display related site option when the selected policy locality is preferred or non-preferred.
      const siteLocation: AffinitySiteLocation = this.getAffinitySiteLocation(this.getPolicy().locality);
      this.locations.forEach(location => {
         location.isVisible = this.isPolicyWithLocality ? location.id === siteLocation : true;
      });

      // Change target location to related site if the selected policy has locality.
      if (this.isPolicyWithLocality) {
         this.iscsiTarget.patchValue({
            [this.LOCATION_CONTROL_NAME]: siteLocation,
         });
      }

      this.updateLocationAlert();
   };

   /**
    * Get the AffinitySiteLocation option for the the select element.
    * We only need to check if the site configured in policy is preferred or secondary,
    * and only display the preferred or secondary site according to the policy setting (PR 2713993).
    * In other cases the select element is hidden or it displays all the 3 options
    */
   private getAffinitySiteLocation(locality: string): AffinitySiteLocation {
      switch (locality) {
         case StoragePolicyLocality.PREFERRED: return AffinitySiteLocation.PREFERRED_DOMAIN;
         case StoragePolicyLocality.SECONDARY: return AffinitySiteLocation.NON_PREFERRED_DOMAIN;
         default: return null;
      }
   }

   updateLocationAlert = () => {
      if (this.isPolicyWithLocality) {
         switch (this.getPolicy().locality) {
            case StoragePolicyLocality.PREFERRED:
               this.locationAlert = VsanUiUtils.getString(
                     "vsan.configure.iscsiTargets.target.dialog.info.policyWithLocality.preferred");
               return;
            case StoragePolicyLocality.SECONDARY:
               this.locationAlert = VsanUiUtils.getString(
                     "vsan.configure.iscsiTargets.target.dialog.info.policyWithLocality.secondary");
               return;
         }
      } else if (this.getLocation() === AffinitySiteLocation.PREFERRED_DOMAIN ||
            this.getLocation() === AffinitySiteLocation.NON_PREFERRED_DOMAIN) {
         this.locationAlert = VsanUiUtils.getString("vsan.configure.iscsiTargets.target.dialog.info.sameSite");
      } else {
         this.locationAlert = null;
      }
   };

   get isPolicyWithLocality(): boolean {
      return !!this.getPolicy() && this.getPolicy().locality &&
            this.getPolicy().locality !== StoragePolicyLocality.NONE;
   }

   ok = () => {
      this.iscsiTarget.get(this.AUTH_SPEC_CONTROL_NAME).updateValueAndValidity();
      if (this.iscsiTarget.invalid) {
         // Make sure any errors from policy preselection are displayed
         this.iscsiSettingsForm.markAsTouched();
         return;
      }

      this.loading = true;
      if (this.originalTarget) {
         const newAlias: string = (this.getAlias() === this.originalTarget.alias) ? null : this.getAlias();
         const targetLocation: AffinitySiteLocation =
               this.isTargetLocationSupported && this.getLocation() !== this.originalTarget.affinityLocation ?
                     this.getLocation() : null;
         const editSpec: TargetOperationSpec = TargetOperationSpec.Factory.create(
               this.originalTarget.alias,
               this.getAuthSpec(),
               this.originalTarget.iqn,
               this.getNetwork(),
               newAlias,
               this.getPort(),
               this.getPolicy().id,
               targetLocation,
         );
         this.targetMutationProvider.editTarget(this.clusterRef, editSpec).then(this.close).catch(this.handleError);
      } else {
         // When selecting None option from dialog, use "null" as the location to create a new iSCSI target,
         // use "None" only when changing location from preferred or non-preferred to None
         // because "None" will trigger the target location cleanup in backend, but "null" won't.
         const targetLocation: AffinitySiteLocation =
               this.isTargetLocationSupported && this.getLocation() !== AffinitySiteLocation.NONE ?
                     this.getLocation() : null;
         const addSpec: TargetOperationSpec = TargetOperationSpec.Factory.create(
               this.getAlias(),
               this.getAuthSpec(),
               this.getIqn(),
               this.getNetwork(),
               null,
               this.getPort(),
               this.getPolicy().id,
               targetLocation,
         );
         this.targetMutationProvider.addTarget(this.clusterRef, addSpec).then(this.close).catch(this.handleError);
      }
   };

   private getAlias(): string {
      return this.iscsiTarget && this.iscsiTarget.get(this.ALIAS_CONTROL_NAME).value;
   }

   private getAuthSpec(): VsanIscsiAuthSpec {
      return this.iscsiTarget && this.iscsiTarget.get(this.AUTH_SPEC_CONTROL_NAME).value;
   }

   private getIqn(): string {
      return this.iscsiTarget && this.iscsiTarget.get(this.IQN_CONTROL_NAME).value;
   }

   private getNetwork(): string {
      return this.iscsiTarget && this.iscsiTarget.get(this.NETWORK_CONTROL_NAME).value;
   }

   private getPort(): number {
      return this.iscsiTarget && this.iscsiTarget.get(this.PORT_CONTROL_NAME).value;
   }

   private getPolicy(): StoragePolicyData {
      return this.iscsiTarget && this.iscsiTarget.get(this.POLICY_CONTROL_NAME).value;
   }

   private getLocation(): AffinitySiteLocation {
      return this.iscsiTarget && this.iscsiTarget.get(this.LOCATION_CONTROL_NAME) &&
            this.iscsiTarget.get(this.LOCATION_CONTROL_NAME).value;
   }

   private iqnValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      if (!this.iscsiTarget) {
         return null;
      }
      // When editing target, the target iqn is not editable, do not need to verify it.
      if (this.originalTarget) {
         return null;
      }
      // If the user is adding a target, verify the target iqn.
      if (!VsanIscsiUtil.validateIqn(control.value)) {
         return {[this.IQN_VALIDATION]: {value: control.value}};
      }
      return null;
   };

   private aliasValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      if (!this.iscsiTarget) {
         return null;
      }

      this.aliasErrorMessage = VsanIscsiUtil.getAliasValidationMessage(control.value);
      if (this.aliasErrorMessage) {
         return {[this.ALIAS_VALIDATION]: {value: control.value}};
      }
   };

   private policyValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      if (!this.iscsiTarget) {
         return null;
      }
      this.policyErrorMessage = VsanIscsiUtil.validatePolicy(control.value,
            VsanUiUtils.getString("vsan.configure.iscsiTargets.target.dialog.edit.policy.empty"));
      if (this.policyErrorMessage) {
         return {[this.POLICY_VALIDATION]: {value: control.value}};
      }
      return null;
   };

   private getPolicyByUuid(policyUuid: string): StoragePolicyData {
      if (!policyUuid) {
         return null;
      }

      return this.availablePolicies.find(policy => {
         return policy.id === policyUuid;
      });
   }

   private getDefaultPolicy(): StoragePolicyData {
      return this.availablePolicies.find(policy => {
         return policy.isDefault;
      });
   }

   private handleError = (error: string): void => {
      this.errorMessages = [error];
      this.loading = false;
   };

   get blockApplyButton(): boolean {
      return this.loading
            || this.errorMessages && this.errorMessages.length > 0
            || (this.iscsiTarget.touched && this.iscsiTarget.invalid);
   }

   handleAuthChange = (type: IscsiTargetAuthType): void => {
      // Change the dialog height according to the authentication type.
      switch (type) {
         case IscsiTargetAuthType.CHAP:
            VsanUiUtils.setModalDialogHeight(this.chapHeight);
            break;
         case IscsiTargetAuthType.CHAP_Mutual:
            VsanUiUtils.setModalDialogHeight(this.chapMutualHeight);
            break;
         default:
            VsanUiUtils.setModalDialogHeight(this.defaultHeight);
      }
      // After changing the authentication selection, the form has been touched, but sometimes its touched property
      // has no chagne (for example, changing authentication from None to CHAP directly), so reset it manually.
      this.iscsiTarget.markAllAsTouched();
   };

   private updateDialogHeights(): void {
      if (!this.originalTarget) {
         this.defaultHeight += this.iqnConventionHeight;
         this.chapHeight += this.iqnConventionHeight;
         this.chapMutualHeight += this.iqnConventionHeight;
      }
      if (this.isTargetLocationSupported) {
         this.defaultHeight += this.locationHeight;
         this.chapHeight += this.locationHeight;
         this.chapMutualHeight += this.locationHeight;
      }
   }

   private close = (taskRef: ManagedObject = null) => {
      VsanUiUtils.closeModalDialog(taskRef);
   };
}

export enum StoragePolicyLocality {
   NONE = "None",
   PREFERRED = "Preferred Fault Domain",
   SECONDARY = "Secondary Fault Domain",
}
