import {ClrDatagridComparatorInterface} from "@clr/angular";
import {IscsiTarget} from "@generated/iscsi-target";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";

export class AuthTypeComparator implements ClrDatagridComparatorInterface<IscsiTarget> {
   compare = (t1: IscsiTarget, t2: IscsiTarget): number => {
      return VsanIscsiUtil.getAuthenticationLabel(t1.authType).localeCompare(
            VsanIscsiUtil.getAuthenticationLabel(t2.authType));
   };
}