import {VsanIscsiUtil} from "../../common/util/vsan-iscsi-util";
import {TargetInitiatorSpec} from "@generated/target-initiator-spec";
import {ClrDatagridComparatorInterface} from "@clr/angular";

export class IsGroupComparator implements ClrDatagridComparatorInterface<TargetInitiatorSpec> {
   compare = (s1: TargetInitiatorSpec, s2: TargetInitiatorSpec): number => {
      return VsanIscsiUtil.getAllowedInitiatorsType(s1.isGroup).localeCompare(
            VsanIscsiUtil.getAllowedInitiatorsType(s2.isGroup));
   };
}