import {IscsiTarget} from "@generated/iscsi-target";
import {InitiatorGroupTarget} from "@generated/initiator-group-target";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

export class IqnFilter implements ClrDatagridStringFilterInterface<IscsiTarget | InitiatorGroupTarget> {
   accepts = (target: IscsiTarget | InitiatorGroupTarget, search: string): boolean => {
      return target.iqn.toLowerCase().indexOf(search) >= 0;
   };
}