import {Component, EventEmitter, Input, Output} from "@angular/core";
import {Icon} from "@util/icon";
import {IscsiLun} from "@generated/iscsi-lun";
import {VsanObject} from "@generated/vsan-object";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";
import {VsanUiUtils} from "@util/vsan-util";
import {IscsiLunStatus} from "@generated/iscsi-lun-status";
import {ManagedObject} from "@service/managed-object";
import {VsanIscsiTargetPropertyProvider} from "@generated/vsan-iscsi-target-property-provider";
import {ModalBuilder} from "@util/modal-builder";
import {LunOperationSpec} from "@generated/lun-operation-spec";
import {VsanIscsiTargetMutationProvider} from "@generated/vsan-iscsi-target-mutation-provider";
// Import filters
import {LunIdFilter} from "../filters/lun-id-filter";
import {AliasFilter} from "../filters/alias-filter";
import {LunSizeFilter} from "../filters/lun-size-filter";
import {ActualSizeFilter} from "../filters/actual-size-filter";
import {LunStateFilter} from "../filters/lun-state-filter";
import {HealthFilter} from "../filters/health-filter";
import {StoragePolicyFilter} from "../filters/storage-policy-filter";
import {ComplianceResultFilter} from "../filters/compliance-result-filter";
// Import comparators
import {LunStateComparator} from "../comparators/lun-state-comparator";
import {HealthComparator} from "../comparators/health-comparator";
import {ComplianceResultComparator} from "../comparators/compliance-result-comparator";
import {TaskMonitorService} from "@service/task-monitor-service";
import {EditLunStateContext} from "@navigation/context/edit-lun-state-context";

@Component({
   selector: "vsan-lun-list",
   templateUrl: "lun-list.html",
   styleUrls: ["lun-list.scss", "iscsi-common.scss"],
})
export class LunListComponent {

   private static ONLINE_DIALOG_WIDTH: number = 540;
   private static ONLINE_DIALOG_HEIGHT: number = 200;
   private static OFFLINE_DIALOG_WIDTH: number = 550;
   private static OFFLINE_DIALOG_HEIGHT: number = 240;

   @Output()
   lunsOperationFailed: EventEmitter<string> = new EventEmitter<string>();

   @Output()
   changeLunCount: EventEmitter<string> = new EventEmitter<string>();

   Icon = Icon;
   VsanIscsiUtil = VsanIscsiUtil;

   isUpdatingLuns: boolean;
   selectedLuns: IscsiLun[] = [];

   private _targetAlias: string;

   @Input()
   set targetAlias(alias: string) {
      if (this._targetAlias === alias) {
         return;
      }
      this._targetAlias = alias;
      this.fetchLunList();
   }

   get targetAlias(): string {
      return this._targetAlias;
   }

   private _luns: IscsiLun[] = [];

   set luns(value: IscsiLun[]) {
      if (!value || value.length === 0) {
         this._luns = [];
         this.selectedLuns = [];
         return;
      }
      this._luns = value;
      const ids: number[] = this.selectedLuns.map(lun => {
         return lun.lunId;
      });
      this.selectedLuns = ids.map(id => this.luns.find(lun => {
         return lun.lunId === id;
      }));
   }

   get luns(): IscsiLun[] {
      return this._luns;
   }

   @Input()
   hasComputePrivileges: boolean = false;

   @Input()
   readPoliciesPermission: boolean = false;

   // iSCSI LUN list filter
   lunIdFilter: LunIdFilter = new LunIdFilter();
   aliasFilter: AliasFilter = new AliasFilter();
   capacityFilter: LunSizeFilter = new LunSizeFilter();
   usedCapacityFilter: ActualSizeFilter = new ActualSizeFilter();
   stateFilter: LunStateFilter = new LunStateFilter();
   healthFilter: HealthFilter = new HealthFilter();
   storagePolicyFilter: StoragePolicyFilter = new StoragePolicyFilter();
   complianceResultFilter: ComplianceResultFilter = new ComplianceResultFilter();

   // iSCSI LUN list comparator
   stateComparator: LunStateComparator = new LunStateComparator();
   healthComparator: HealthComparator = new HealthComparator();
   complianceResultComparator: ComplianceResultComparator = new ComplianceResultComparator();

   constructor(private taskSvc: TaskMonitorService,
         private iscsiTargetProvider: VsanIscsiTargetPropertyProvider,
         private targetMutationProvider: VsanIscsiTargetMutationProvider) {
   }

   private fetchLunList = (): void => {
      this.isUpdatingLuns = true;
      this.iscsiTargetProvider.getVsanIscsiTargetLunList(ManagedObject.contextObject, this.targetAlias)
            .then((lunList: IscsiLun[]) => {
               this.luns = lunList;
               this.isUpdatingLuns = false;
            })
            .catch((error: string) => {
               this.luns = [];
               this.isUpdatingLuns = false;
               this.lunsOperationFailed.emit(error);
            });
   };

   get disableActions(): boolean {
      return this.isUpdatingLuns || !this.hasComputePrivileges;
   }

   get onlineActionVisible(): boolean {
      return this.selectedLuns.length === 1 && this.selectedLuns[0] &&
            this.selectedLuns[0].status === IscsiLunStatus.Offline;
   }

   get offlineActionVisible(): boolean {
      return this.selectedLuns.length === 1 && this.selectedLuns[0] &&
            this.selectedLuns[0].status === IscsiLunStatus.Online;
   }

   get lunListTitle(): string {
      return this.targetAlias ? VsanUiUtils.getString("vsan.configure.iscsiTargets.lun.title", this.targetAlias) :
            VsanUiUtils.getString("vsan.configure.iscsiTargets.no.target.lun.title");
   }

   get reapplyActionVisible(): boolean {
      return this.selectedLuns.some((lun: IscsiLun) => {
         return this.hasMismatchComplianceResult(lun);
      });
   }

   actionTitle = (descriptionKey: string): string => {
      return VsanUiUtils.getString(!this.hasComputePrivileges ? "vsan.common.nopermissions" : descriptionKey);
   };

   complianceResultVisible = (lun: any): boolean => {
      return lun.storagePolicy && lun.complianceResult;
   };

   openAddLunDialog = (): void => {
      const suggestedLunId: number = this.caculateSuggestedId();
      if (suggestedLunId === -1) {
         this.lunsOperationFailed
               .emit(VsanUiUtils.getString("vsan.configure.iscsiTargets.lun.dialog.id.cannotSuggest"));
         return;
      }
      new ModalBuilder("iscsiTargets/addLun")
            .setSize(620, 340)
            .open({
               targetAlias: this.targetAlias,
               suggestedLunId: suggestedLunId,
            }).then(this.onLunsChanged);
   };

   openEditLunDialog = (): void => {
      new ModalBuilder("iscsiTargets/editLun")
            .setSize(620, 340)
            .open({
               lun: this.selectedLuns[0],
               targetAlias: this.targetAlias,
               alreadyUsedIds: this.luns.map(lun => {
                  return lun.lunId;
               }),
            }).then(this.onLunsChanged);
   };

   private caculateSuggestedId = (): number => {
      // The maximum number of LUN is 256.
      if (this.luns.length >= 256) {
         return -1;
      }
      const usedIds: number[] = this.luns.map((lun: IscsiLun) => {
         return lun.lunId;
      });
      for (let index: number = 0; index < 256; index++) {
         if (usedIds.indexOf(index) === -1) {
            return index;
         }
      }
      return -1;
   };

   placeLunOnline = () => {
      this.placeLunStatus(IscsiLunStatus.Online);
   };

   placeLunOffline = () => {
      this.placeLunStatus(IscsiLunStatus.Offline);
   };

   private placeLunStatus(newStatus: IscsiLunStatus) {
      const height: number = newStatus === IscsiLunStatus.Online ?
            LunListComponent.ONLINE_DIALOG_HEIGHT : LunListComponent.OFFLINE_DIALOG_HEIGHT;
      const width: number = newStatus === IscsiLunStatus.Online ?
            LunListComponent.ONLINE_DIALOG_WIDTH : LunListComponent.OFFLINE_DIALOG_WIDTH;

      const lun: IscsiLun = this.selectedLuns[0];
      const editSpec: LunOperationSpec = LunOperationSpec.Factory.create(
            this.targetAlias,
            lun.alias,
            lun.lunId,
            VsanIscsiUtil.UNCHANGED_LUN_SIZE_VALUE,
            VsanIscsiUtil.UNCHANGED_NEW_LUN_ID_VALUE,
            newStatus.toString(),
            lun.vmStoragePolicyUuid);
      const context: EditLunStateContext = {
         editSpec: editSpec
      };

      new ModalBuilder("iscsiTargets/editLunState")
            .setSize(width, height)
            .open(context)
            .then(this.onLunsChanged)
            .catch((error: string) => {
               this.lunsOperationFailed.emit(error);
               this.fetchLunList();
            });
   }

   /**
    * Reapply out of date storage policies to the selected LUNs.
    */
   reapplyPolicies = (): void => {
      const mismatchLuns: IscsiLun[] = this.selectedLuns.filter((lun: IscsiLun) => {
         return this.hasMismatchComplianceResult(lun);
      });
      if (mismatchLuns.length === 0) {
         return;
      }

      const promises: Array<Promise<ManagedObject>> = mismatchLuns.map((lun: IscsiLun) => {
         return this.targetMutationProvider.editLun(ManagedObject.contextObject, LunOperationSpec.Factory.create(
               this.targetAlias,
               lun.alias,
               lun.lunId,
               VsanIscsiUtil.UNCHANGED_LUN_SIZE_VALUE,
               VsanIscsiUtil.UNCHANGED_NEW_LUN_ID_VALUE,
               lun.status.toString(),
               lun.vmStoragePolicyUuid));
      });
      Promise.all(promises).then((runningTaskRefs: ManagedObject[]) => {
         const completedTasks: Array<Promise<ManagedObject>> =
               runningTaskRefs.map(task => this.taskSvc.getAwaitResult(task));
         Promise.all(completedTasks)
               .then(this.fetchLunList)
               .catch((error) => {
                  this.fetchLunList();
                  this.lunsOperationFailed.emit(error);
               });
      });
   };

   private hasMismatchComplianceResult = (lun: any): boolean => {
      const lunObject: VsanObject = lun as VsanObject;
      return lunObject && lunObject.storagePolicy && lunObject.complianceResult && lunObject.complianceResult.mismatch;
   };

   openRemoveLunDialog = (): void => {
      const lunIds: number[] = this.selectedLuns.map((lun: IscsiLun) => {
         return lun.lunId;
      });
      if (lunIds.length === 0) {
         return;
      }
      new ModalBuilder("iscsiTargets/removeLuns")
            .setSize(530, 260)
            .open({
               lunIds: lunIds,
               targetAlias: this.targetAlias,
            })
            .then(this.onLunsChanged)
            .catch((error: string) => {
               this.lunsOperationFailed.emit(error);
            });
   };

   private onLunsChanged = (param: any): void => {
      const tasks: ManagedObject[] = param.tasks;
      const refreshTargetList: boolean = param.refreshTargetList;
      const aliasOfChangedTarget: string = param.aliasOfChangedTarget;

      if (!tasks || tasks.length === 0 || tasks.filter(t => !!t).length === 0 || !aliasOfChangedTarget) {
         return;
      }
      const promises: Array<Promise<any>> = tasks.map(t => this.taskSvc.getAwaitResult(t));
      Promise.all(promises).then(() => {
         if (refreshTargetList) {
            this.changeLunCount.emit(aliasOfChangedTarget);
         }
         if (aliasOfChangedTarget === this.targetAlias) {
            this.fetchLunList();
         }
      }).catch((error: string) => {
         this.lunsOperationFailed.emit(error);
      });
   };
}