import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {Logger} from "@util/logger";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";
import {InitiatorGroupTarget} from "@generated/initiator-group-target";
import {IscsiTarget} from "@generated/iscsi-target";
import {VsanIscsiInitiatorGroupMutationProvider} from "@generated/vsan-iscsi-initiator-group-mutation-provider";
import {VsanIscsiTargetPropertyProvider} from "@generated/vsan-iscsi-target-property-provider";
import {InitiatorGroupTargetAdditionSpec} from "@generated/initiator-group-target-addition-spec";
import {IqnFilter} from "../filters/iqn-filter";
import {AliasFilter} from "../filters/alias-filter";
import {AuthTypeFilter} from "../filters/auth-type-filter";
import {AuthTypeComparator} from "../comparators/auth-type-comparator";

@Component({
   templateUrl: "add-accessible-target.html",
   styleUrls: ["add-accessible-target.scss"],
   selector: "vsan-iscsi-add-target",
})
export class AddAccessibleTargetComponent implements OnInit {
   VsanIscsiUtil: typeof VsanIscsiUtil = VsanIscsiUtil;

   accessibleTargets: InitiatorGroupTarget[];
   initiatorGroupName: string;

   allTargets: IscsiTarget[];
   selectedTargets: IscsiTarget[] = [];
   alertMessages: string[];
   loading: boolean;
   clusterRef: ManagedObject;

   currentListState: TargetListState = TargetListState.ALL_AVAILABLE;
   TargetListState: typeof TargetListState = TargetListState;

   iqnFilter: IqnFilter = new IqnFilter();
   aliasFilter: AliasFilter = new AliasFilter();
   authTypeFilter: AuthTypeFilter = new AuthTypeFilter();
   authTypeComparator: AuthTypeComparator = new AuthTypeComparator();

   constructor(private targetPropertyProvider: VsanIscsiTargetPropertyProvider,
         private initiatorGroupMutationProvider: VsanIscsiInitiatorGroupMutationProvider) {}

   ngOnInit(): void {
      this.clusterRef = ManagedObject.contextObject;

      this.accessibleTargets = VsanUiUtils.getModalContext().accessibleTargets;
      this.initiatorGroupName = VsanUiUtils.getModalContext().initiatorGroupName;

      this.fetch();
   }

   private fetch = (): void => {
      this.loading = true;
      this.targetPropertyProvider.getIscsiTargets(this.clusterRef)
            .then(this.handleResponse)
            .catch(this.handleError);
   };

   private handleResponse = (targets: IscsiTarget[]): void => {
      this.loading = false;
      this.allTargets = targets;
   };

   private handleError = (error: string): void => {
      this.loading = false;
      Logger.error(error);
      this.alertMessages = [error || VsanUiUtils.getString("vsan.common.error.dataExtraction")];
   };

   getTargetList = (): IscsiTarget[] => {
      switch (+this.currentListState) {
         case TargetListState.ALL_AVAILABLE:
            /**
             * all available = all targets - allowed targets
             */
            if (this.allTargets == null) {
               return null;
            }
            return this.allTargets.filter(target =>
                  !this.accessibleTargets.some(allowedTarget => allowedTarget.alias === target.alias));
         case TargetListState.SELECTED:
            return this.selectedTargets;
         default:
            throw new Error("Unsupported target list state: " + this.currentListState);
      }
   };

   ok = (): void => {
      const addTargetSpec: InitiatorGroupTargetAdditionSpec = InitiatorGroupTargetAdditionSpec.Factory.create(
         this.initiatorGroupName,
         this.selectedTargets.map(target => target.alias),
      );
      this.initiatorGroupMutationProvider.addTarget(this.clusterRef, addTargetSpec)
            .then((res) => {
               Logger.log("success", res);
               VsanUiUtils.closeModalDialog(true);
            })
            .catch((err) => {
               Logger.log("error", err);
               this.handleError(err);
            });
   };

   validate = (): boolean => !!this.selectedTargets?.length;
}

export enum TargetListState {
   ALL_AVAILABLE, SELECTED,
}