import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {InitiatorGroupTargetRemoveSpec} from '@generated/initiator-group-target-remove-spec';
import {VsanIscsiInitiatorGroupMutationProvider} from "@generated/vsan-iscsi-initiator-group-mutation-provider";

@Component({
   templateUrl: "remove-accessible-target.html",
   selector: "vsan-iscsi-remove-accessible-target",
   styleUrls: ["remove-accessible-target.scss"]
})

export class RemoveAccessibleTargetComponent implements OnInit {
   targetAlias: string;
   groupName: string;
   isRemoving: boolean;
   alertMessages: string[];

   constructor (private initiatorGroupMutationProvider: VsanIscsiInitiatorGroupMutationProvider) {}

   ngOnInit() {
      this.groupName = VsanUiUtils.getModalContext().groupName;
      this.targetAlias = VsanUiUtils.getModalContext().targetAlias;
   }

   ok = (): void => {
      let removeSpec: InitiatorGroupTargetRemoveSpec = InitiatorGroupTargetRemoveSpec.Factory.create(
            this.groupName, this.targetAlias);
      this.isRemoving = true;
      this.initiatorGroupMutationProvider.removeTarget(ManagedObject.contextObject, removeSpec)
            .then(() => {
               this.isRemoving = false;
               VsanUiUtils.closeModalDialog(true);
            })
            .catch((error: string) => {
               this.isRemoving = false;
               this.alertMessages = [error];
            });
   };
}
