import {InitiatorGroup} from "@generated/initiator-group";
import {ClrDatagridComparatorInterface} from "@clr/angular";

export class InitiatorsComparator implements ClrDatagridComparatorInterface<InitiatorGroup> {
   compare = (g1: InitiatorGroup, g2: InitiatorGroup): number => {
      return g1.initiators.length - g2.initiators.length;
   };
}