
export interface LunOperationSpec {

   targetAlias: string;
   lunAlias: string;
   lunId: number;
   lunSize: number;
   newLunId: number;
   status: string;
   policyId: string;
} //class LunOperationSpec

export module LunOperationSpec{
export class Factory {

   static create = (targetAlias: string, lunAlias: string, lunId: number, lunSize: number, newLunId: number, status: string, policyId: string): LunOperationSpec => {
      return { targetAlias: targetAlias, lunAlias: lunAlias, lunId: lunId, lunSize: lunSize, newLunId: newLunId, status: status, policyId: policyId, };
   }


} //class Factory

} //module LunOperationSpec
