import {VsanObject} from "@generated/vsan-object";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";
import {ClrDatagridComparatorInterface} from "@clr/angular";

export class ComplianceResultComparator implements ClrDatagridComparatorInterface<VsanObject> {
   compare = (o1: VsanObject, o2: VsanObject): number => {
      return VsanIscsiUtil.getComplianceLabel(o1.complianceResult).localeCompare(
            VsanIscsiUtil.getComplianceLabel(o2.complianceResult));
   };
}