import {Component, EventEmitter, Input, Output} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {ModalBuilder} from "@util/modal-builder";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";
import {TargetInitiatorSpec} from "@generated/target-initiator-spec";
import {VsanIscsiTargetPropertyProvider} from "@generated/vsan-iscsi-target-property-provider";
import {TaskService} from "@generated/task-service";
import {NameFilter} from "../filters/name-filter";
import {IsGroupFilter} from "../filters/is-group-filter";
import {IsGroupComparator} from "../comparators/is-group-comparator";

@Component({
   selector: "vsan-allowed-initiators",
   templateUrl: "allowed-initiators.html",
   styleUrls: ["allowed-initiators.scss", "iscsi-common.scss"],
})
export class AllowedInitiatorsComponent {
   VsanIscsiUtil = VsanIscsiUtil;

   private static readonly EVERYONE_GROUP: string =
         VsanUiUtils.getString("vsan.configure.iscsiTargets.allowed.initiators.type.everyone");

   loading: boolean;
   allowedInitiators: TargetInitiatorSpec[];
   selectedInitiator: TargetInitiatorSpec;

   @Output()
   private operationFailed: EventEmitter<string> = new EventEmitter<string>();

   private _targetAlias: string;

   @Input()
   set targetAlias(alias: string) {
      if (this.targetAlias === alias) {
         return;
      }
      this._targetAlias = alias;
      this.updateAllowedInitiatorsList();
   }

   get targetAlias(): string {
      return this._targetAlias;
   }

   @Input()
   hasComputePrivileges: boolean;

   nameFilter: NameFilter = new NameFilter();
   typeFilter: IsGroupFilter = new IsGroupFilter();
   typeComparator: IsGroupComparator = new IsGroupComparator();

   constructor(private iscsiTargetProvider: VsanIscsiTargetPropertyProvider,
         private taskService: TaskService) {
   }

   updateAllowedInitiatorsList = (): void => {
      this.loading = true;
      this.selectedInitiator = null;
      this.iscsiTargetProvider.getVsanIscsiTargetInitiatorList(ManagedObject.contextObject, this.targetAlias)
            .then((initiators: TargetInitiatorSpec[]) => {
               this.loading = false;
               if (initiators.length > 0) {
                  this.allowedInitiators = initiators;
               } else {
                  // display everyone as a default initiator group
                  this.allowedInitiators = [TargetInitiatorSpec.Factory.create(
                        AllowedInitiatorsComponent.EVERYONE_GROUP, true),
                  ];
               }
            }).catch((error: string) => {
               this.loading = false;
               this.operationFailed.emit(error);
            });
   };

   get isAddActionDisabled(): boolean {
      return this.loading || !this.hasComputePrivileges || !this.targetAlias;
   }

   get addInitiatorTitle(): string {
      if (!this.hasComputePrivileges) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      if (!this.targetAlias) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.notargetselected");
      }
      return VsanUiUtils.getString("vsan.configure.iscsiTargets.allowed.initiators.add.description");
   }

   get isRemoveActionDisabled(): boolean {
      return this.loading || !this.hasComputePrivileges ||
            this.selectedInitiator.name === AllowedInitiatorsComponent.EVERYONE_GROUP;
   }

   get removeAccessTitle(): string {
      if (!this.hasComputePrivileges) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      if (this.selectedInitiator.name === AllowedInitiatorsComponent.EVERYONE_GROUP) {
         return VsanUiUtils.getString("vsan.configure.iscsiTargets.allowed.initiators.remove.disabled.reason",
               this.selectedInitiator.name);
      }
      return VsanUiUtils.getString("vsan.configure.iscsiTargets.allowed.initiators.remove.description");
   }

   onSelectionChange = (initiatorSpec: TargetInitiatorSpec): void => {
      this.selectedInitiator = initiatorSpec;
   };

   disallowTargetAccess = (): void => {
      new ModalBuilder("iscsiTargets/removeAllowedInitiator")
            .setSize(500, 190)
            .open({
               targetAlias: this.targetAlias,
               initiator: this.selectedInitiator,
            })
            .then((removed: boolean): void => {
               if (removed) {
                  this.selectedInitiator = null;
                  this.updateAllowedInitiatorsList();
               }
            })
            .catch(error => this.operationFailed.emit(error));
   };

   addInitiator = (): void => {
      new ModalBuilder("iscsiTargets/addAllowedInitiator")
            .setSize(720, 580)
            .open({
               targetAlias: this.targetAlias,
               allowedInitiators: this.allowedInitiators.map(initiator => initiator.name),
            })
            .then((dataChanged: boolean): void => {
               if (dataChanged) {
                  this.updateAllowedInitiatorsList();
               }
            })
            .catch(error => this.operationFailed.emit(error));
   };
}