import {VsanObject} from "@generated/vsan-object";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

export class StoragePolicyFilter implements ClrDatagridStringFilterInterface<VsanObject> {
   accepts = (obj: VsanObject, search: string): boolean => {
      return obj.storagePolicy !== null && obj.storagePolicy.toLowerCase().indexOf(search) > -1;
   };
}