import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";
import {ManagedObject} from "@service/managed-object";
import {VsanIscsiTargetMutationProvider} from "@generated/vsan-iscsi-target-mutation-provider";
import {TargetInitiatorEditSpec} from "@generated/target-initiator-edit-spec";
import {InitiatorGroup} from "@generated/initiator-group";
import {TargetInitiatorRemoveSpec} from "@generated/target-initiator-remove-spec";
import {VsanIscsiInitiatorGroupPropertyProvider} from "@generated/vsan-iscsi-initiator-group-property-provider";

@Component({
   templateUrl: "add-target-initiator.html",
   styleUrls: ["./add-target-initiator.scss"],
   selector: "vsan-iscsi-add-allowed-initiator"
})
export class AddTargetInitiatorComponent implements OnInit {
   targetAlias: string;
   private allowedInitiators: string[];

   initiatorsInputStr: string = "";
   initiatorGroupList: InitiatorGroup[];
   selectedInitiatorGroups: InitiatorGroup[] = [];

   selectedInitiatorType: AllowedInitiatorType = AllowedInitiatorType.EVERYONE;
   allowedType = AllowedInitiatorType;

   loading: boolean = false;
   alertMessages: string[];

   constructor(private propertyProvider: VsanIscsiInitiatorGroupPropertyProvider,
      private mutationProvider: VsanIscsiTargetMutationProvider) {}

   ngOnInit() {
      this.targetAlias = VsanUiUtils.getModalContext().targetAlias;
      this.allowedInitiators = VsanUiUtils.getModalContext().allowedInitiators;
      if (this.allowedInitiators.length === 1) {
         this.allowedInitiators = this.allowedInitiators.filter(
               initiator => initiator !=
                     VsanUiUtils.getString("vsan.configure.iscsiTargets.allowed.initiators.type.everyone"));
      }

      this.loading = true;
      let clusterRef: ManagedObject = ManagedObject.contextObject;
      this.propertyProvider.getVsanIscsiInitiatorGroupList(clusterRef)
            .then((list): void => {
               this.loading = false;
               // Include only initiators that are not already in the allowed initiators list.
               this.initiatorGroupList = (list === null) ? [] : list.filter(group => {
                  return this.allowedInitiators.indexOf(group.name) === -1;
               });
            }).catch(this.handleError)
   }

   ok = () => {
      if (!this.validate()) {
         return;
      }
      let clusterRef: ManagedObject = ManagedObject.contextObject;
      /**
       * everyone access means we need to remove all the allowed initiators
       */
      if (this.selectedInitiatorType === AllowedInitiatorType.EVERYONE) {
         if (this.allowedInitiators.length === 0) {
            //the target was everyone access, do nothing
            VsanUiUtils.closeModalDialog(false);
            return;
         }
         const removeSpec: TargetInitiatorRemoveSpec =
            TargetInitiatorRemoveSpec.Factory.create(this.targetAlias, this.allowedInitiators);
         this.mutationProvider.disallowInitiatorAccess(clusterRef, removeSpec)
            .then(() => {
               /**
                * mutationProvider.disallowInitiatorAccess doesn't create a backend task, so we just pass true
                */
               VsanUiUtils.closeModalDialog(true);
            })
            .catch(this.handleError);
         return;
      }

      const spec: TargetInitiatorEditSpec = TargetInitiatorEditSpec.Factory.create(
            this.targetAlias, this.getInitiatorsFromInput());
      this.mutationProvider.allowInitiatorAccess(clusterRef, spec)
            .then(() => {
               /**
                * mutationProvider.allowInitiatorAccess doesn't create a backend task, so we just pass true
                */
               VsanUiUtils.closeModalDialog(true);
            })
            .catch(this.handleError);
   };

   close = () => {
      VsanUiUtils.closeModalDialog(false);
   };

   private validate = (): boolean => {
      switch(this.selectedInitiatorType) {
         case AllowedInitiatorType.INITIATOR:
            let iqns: string[] = this.getInitiatorsFromInput();
            if (iqns.length == 0) {
               this.alertMessages =
                     [VsanUiUtils.getString("vsan.configure.iscsiTargets.allowed.initiators.type.initiator.iqn.empty.error")];
               return false;
            }
            if (iqns.some(iqn => !VsanIscsiUtil.validateIqn(iqn.trim()))) {
               this.alertMessages =
                     [VsanUiUtils.getString("vsan.configure.iscsiTargets.allowed.initiators.type.initiator.iqn.error")];
               return false;
            }
            // Display an error message when the entered IQN already exists in the target.
            const invalidIQNs: string[] = iqns.filter(iqn => {
               return this.allowedInitiators.indexOf(iqn) > -1;
            });
            if (invalidIQNs.length === 1) {
               this.alertMessages = [VsanUiUtils.getString(
                     "vsan.configure.iscsiTargets.allowed.initiators.type.initiator.iqn.repeated.singular",
                     invalidIQNs[0], this.targetAlias)];
               return false;
            } else if (invalidIQNs.length > 1) {
               this.alertMessages = [VsanUiUtils.getString(
                  "vsan.configure.iscsiTargets.allowed.initiators.type.initiator.iqn.repeated.plural",
                  invalidIQNs.join(", "), this.targetAlias)];
               return false;
            }
            break;
         case AllowedInitiatorType.GROUP:
            if (this.selectedInitiatorGroups.length == 0) {
               this.alertMessages=
                  [VsanUiUtils.getString("vsan.configure.iscsiTargets.allowed.initiators.type.group.notSelected.error")];
               return false;
            }
            break;
         default:
            this.alertMessages = null;
            return true;
      }
      return true;
   };

   private handleError = (error: any) => {
      this.loading = false;
      this.alertMessages = [error];
   };

   private getInitiatorsFromInput = (): string[] => {
      switch(this.selectedInitiatorType) {
         case AllowedInitiatorType.EVERYONE:
            return [];
         case AllowedInitiatorType.INITIATOR:
            return this.initiatorsInputStr.trim().split(",")
                  .filter(iqn => !!iqn.trim()) //remove the blank string
                  .map(iqn => iqn.trim()); //trim the iqn string
         case AllowedInitiatorType.GROUP:
            return this.selectedInitiatorGroups.map(group => group.name);
         default:
            throw new Error("Unsupported value found: " + this.selectedInitiatorType);
      }
   };
}

export enum AllowedInitiatorType {
   EVERYONE, INITIATOR, GROUP
}