import {IscsiTarget} from "@generated/iscsi-target";
import {ClrDatagridComparatorInterface} from "@clr/angular";

export class IoOwnerHostComparator implements ClrDatagridComparatorInterface<IscsiTarget> {
   constructor(private addtionalData: Map<string, string>) {}

   compare = (t1: IscsiTarget, t2: IscsiTarget): number => {
      return this.addtionalData.get(t1.ioOwnerHost).localeCompare(this.addtionalData.get(t2.ioOwnerHost));
   };
}