import {Component, OnInit, ViewChild} from "@angular/core";
import {
   AbstractControl,
   FormBuilder,
   FormControl,
   FormGroup,
   ValidationErrors,
   ValidatorFn,
   Validators,
} from "@angular/forms";
import {ClrForm} from "@clr/angular";
import {IscsiLun} from "@generated/iscsi-lun";
import {IscsiLunStatus} from "@generated/iscsi-lun-status";
import {LunOperationSpec} from "@generated/lun-operation-spec";
import {PbmDataProvider} from "@generated/pbm-data-provider";
import {StoragePolicyData} from "@generated/storage-policy-data";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {VsanIscsiTargetMutationProvider} from "@generated/vsan-iscsi-target-mutation-provider";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {StorageSizeMetric} from "@util/storage-size-metric";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "edit-lun.html",
   styleUrls: ["lun-operation-dialog.scss"],
   selector: "vsan-iscsi-edit-lun",
})
export class EditLunComponent implements OnInit {
   IscsiLunStatus: typeof IscsiLunStatus = IscsiLunStatus;

   clusterRef: ManagedObject;

   @LoadingIndicator()
   loading: boolean;

   @ErrorMessage()
   errorMessage: string;

   targetAlias: string;
   editingLun: IscsiLun;
   private alreadyUsedIds: number[];

   iscsiLun: FormGroup;
   availableUnits: StorageSizeMetric[] = [StorageSizeMetric.MB, StorageSizeMetric.GB, StorageSizeMetric.TB];
   availablePolicies: StoragePolicyData[];
   isIscsiOnlineResizeSupported: boolean = false;

   // Control the validation message for the input fileds.
   idError: string;
   aliasError: string;
   sizeError: string;
   policyError: string;

   @ViewChild(ClrForm, {static: true})
   lunSettingsForm: ClrForm;

   readonly ID_CONTROL_NAME: string = "id";
   readonly ALIAS_CONTROL_NAME: string = "alias";
   readonly POLICY_UUID_CONTROL_NAME: string = "policyUuid";
   readonly SIZE_CONTROL_NAME: string = "size";
   readonly SIZE_UNIT_CONTROL_NAME: string = "sizeUnit";
   readonly SIZE_AND_UNITS_GROUP_NAME: string = "sizeAndUnits";

   constructor(private formBuilder: FormBuilder,
         private pbmProvider: PbmDataProvider,
         private capabilityProvider: VsanCapabilityProvider,
         private targetMutationProvider: VsanIscsiTargetMutationProvider) {
      this.iscsiLun = this.formBuilder.group({
         [this.ID_CONTROL_NAME]: new FormControl(null, [Validators.required, this.idValidator]),
         [this.ALIAS_CONTROL_NAME]: new FormControl("", [this.aliasValidator]),
         [this.POLICY_UUID_CONTROL_NAME]: new FormControl("", [this.policyValidator]),
         [this.SIZE_AND_UNITS_GROUP_NAME]: this.formBuilder.group({
            [this.SIZE_CONTROL_NAME]: new FormControl(null, Validators.required),
            [this.SIZE_UNIT_CONTROL_NAME]: new FormControl(StorageSizeMetric.GB),
         }, {validators: this.sizeValidator}),
      });
   }

   @Loader()
   async ngOnInit(): Promise<void> {
      this.clusterRef = ManagedObject.contextObject;
      const context: any = VsanUiUtils.getModalContext();
      this.editingLun = context.lun;
      this.targetAlias = context.targetAlias;
      this.alreadyUsedIds = context.alreadyUsedIds;

      await this.retrieveData();
      this.fillOutFormWithLunData();
   }

   private async retrieveData(): Promise<void> {
      [this.availablePolicies, this.isIscsiOnlineResizeSupported] = await Promise.all([
         this.pbmProvider.getStoragePolicies(this.clusterRef),
         this.capabilityProvider.getIsIscsiOnlineResizeSupported(this.clusterRef),
      ]);
   }

   private fillOutFormWithLunData(): void {
      let size: number;
      let sizeUnit: StorageSizeMetric;
      const sizeInBytes: number = this.editingLun.lunSize;
      if (sizeInBytes % StorageSizeMetric.TB.value === 0) {
         sizeUnit = StorageSizeMetric.TB;
         size = Math.ceil(sizeInBytes / StorageSizeMetric.TB.value);
      } else if (sizeInBytes % StorageSizeMetric.GB.value === 0) {
         sizeUnit = StorageSizeMetric.GB;
         size = Math.ceil(sizeInBytes / StorageSizeMetric.GB.value);
      } else {
         sizeUnit = StorageSizeMetric.MB;
         size = Math.ceil(sizeInBytes / StorageSizeMetric.MB.value);
      }

      const defaultStoragePolicyUuid: string = this.availablePolicies?.find(policy => policy.isDefault)?.id;

      this.iscsiLun.setValue({
         [this.ID_CONTROL_NAME]: this.editingLun.lunId,
         [this.ALIAS_CONTROL_NAME]: this.editingLun.alias,
         [this.POLICY_UUID_CONTROL_NAME]: this.editingLun.vmStoragePolicyUuid || defaultStoragePolicyUuid,
         [this.SIZE_AND_UNITS_GROUP_NAME]: {
            [this.SIZE_CONTROL_NAME]: size,
            [this.SIZE_UNIT_CONTROL_NAME]: sizeUnit,
         },
      });

      // Make sure any errors from policy preselection are displayed.
      this.lunSettingsForm.markAsTouched();
   }

   @Loader()
   async ok(): Promise<void> {
      const editSpec: LunOperationSpec = LunOperationSpec.Factory.create(
            this.targetAlias,
            this.getAlias(),
            this.editingLun.lunId,
            VsanIscsiUtil.UNCHANGED_LUN_SIZE_VALUE,
            VsanIscsiUtil.UNCHANGED_NEW_LUN_ID_VALUE,
            this.editingLun.status.toString(),
            this.editingLun.vmStoragePolicyUuid);
      if (this.getPolicyUuid() && this.getPolicyUuid() !== this.editingLun.vmStoragePolicyUuid) {
         editSpec.policyId = this.getPolicyUuid();
      }
      if (this.editingLun.status === IscsiLunStatus.Offline && this.getId() !== this.editingLun.lunId) {
         editSpec.newLunId = this.getId();
      }
      if (this.isIscsiOnlineResizeSupported || this.editingLun.status === IscsiLunStatus.Offline) {
         const currentSizeInBytes: number = this.getSizeControl().value * this.getSizeUnit().value;
         if (currentSizeInBytes > this.editingLun.lunSize) {
            editSpec.lunSize = currentSizeInBytes;
         }
      }

      const task: ManagedObject = await this.targetMutationProvider.editLun(this.clusterRef, editSpec);
      this.close(task);
   }

   close(task: ManagedObject = null): void {
      VsanUiUtils.closeModalDialog({
         tasks: [task],
         refreshTargetList: false,
         aliasOfChangedTarget: this.targetAlias,
      });
   }

   get sizeHelperMessage(): string {
      const initialLunSize: number = Math.ceil(this.editingLun.lunSize / this.getSizeUnit().value);
      return VsanIscsiUtil.getLunSizeHelperMessage(this.getSizeUnit(), initialLunSize);
   }

   get blockApplyButton(): boolean {
      return this.loading
            || !!this.errorMessage
            || (this.iscsiLun && this.iscsiLun.touched && this.iscsiLun.invalid);
   }

   /**
    * The LUN size cannot be edited when the LUN status is Online and iSCSI online resize is not supported.
    */
   get disableSizeInput(): boolean {
      return this.editingLun.status === IscsiLunStatus.Online && !this.isIscsiOnlineResizeSupported;
   }

   private getId(): number {
      return this.iscsiLun && this.iscsiLun.get(this.ID_CONTROL_NAME).value;
   }

   private getAlias(): string {
      return this.iscsiLun && this.iscsiLun.get(this.ALIAS_CONTROL_NAME).value;
   }

   private getPolicyUuid(): string {
      return this.iscsiLun && this.iscsiLun.get(this.POLICY_UUID_CONTROL_NAME).value;
   }

   private getSizeControl(): AbstractControl {
      return this.iscsiLun?.get(this.SIZE_AND_UNITS_GROUP_NAME)?.get(this.SIZE_CONTROL_NAME);
   }

   private getSizeUnit(): StorageSizeMetric {
      return this.iscsiLun?.get(this.SIZE_AND_UNITS_GROUP_NAME)?.get(this.SIZE_UNIT_CONTROL_NAME).value;
   }

   private idValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      if (!this.iscsiLun) {
         return null;
      }

      // The LUN id can be edited when the LUN status is Offline.
      if (this.editingLun.status === IscsiLunStatus.Offline) {
         if (this.getId() !== this.editingLun.lunId) {
            this.idError = VsanIscsiUtil.getLunIdValidationMessage(this.getId());
            if (this.idError) {
               return {idInvalid: {value: control.value}};
            }
         }

         if (this.alreadyUsedIds) {
            const existingId: number = this.alreadyUsedIds.find((id: number) => id === this.getId());
            if (existingId !== undefined && existingId !== this.editingLun.lunId) {
               this.idError = VsanUiUtils.getString("vsan.configure.iscsiTargets.lun.dialog.id.alreadyUsed");
               return {idInvalid: {value: control.value}};
            }
         }
      }
   };

   private aliasValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      if (!this.iscsiLun) {
         return null;
      }

      // LUN alias can be empty.
      if (control.value) {
         this.aliasError = VsanIscsiUtil.getAliasValidationMessage(control.value);
         if (this.aliasError) {
            return {aliasInvalid: {value: control.value}};
         }
      }
   };

   private policyValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      if (!this.iscsiLun || !this.availablePolicies || this.availablePolicies.length === 0) {
         return null;
      }

      const selectedPolicy: StoragePolicyData = this.availablePolicies.find(policy => {
         return policy.id === control.value;
      });
      if (selectedPolicy) {
         this.policyError = VsanIscsiUtil.getLunPolicyValidationMessage(selectedPolicy);
         if (this.policyError) {
            return {policyInvalid: {value: control.value}};
         }
      } else {
         this.policyError = VsanUiUtils.getString("vsan.configure.iscsiTargets.lun.dialog.edit.policy.empty");
         return {policyInvalid: {value: control.value}};
      }
   };

   private sizeValidator: ValidatorFn = (): ValidationErrors | null => {
      if (!this.iscsiLun) {
         return null;
      }

      const sizeControl: AbstractControl = this.getSizeControl();
      const sizeUnit: StorageSizeMetric = this.getSizeUnit();
      // The LUN size can be edited when iSCSI online resize is supported or the LUN status is Offline.
      if (this.isIscsiOnlineResizeSupported || this.editingLun.status === IscsiLunStatus.Offline) {
         this.sizeError = VsanIscsiUtil.getLunSizeValidationMessage(sizeControl.value, sizeUnit.value);
         if (this.sizeError) {
            const invalidSizeError: ValidationErrors = {
               "sizeInvalid": {value: sizeControl.value},
            };
            sizeControl.setErrors(invalidSizeError);
            return invalidSizeError;
         }

         // The LUN size cannot be changed to a smaller value.
         const currentSizeInBytes: number = sizeControl.value * sizeUnit.value;
         if (currentSizeInBytes < this.editingLun.lunSize) {
            this.sizeError = VsanUiUtils.getString("vsan.configure.iscsiTargets.lun.dialog.edit.growSizeTo.doNotShrink",
                  Math.ceil(this.editingLun.lunSize / sizeUnit.value), sizeUnit);
            const invalidSizeError: ValidationErrors = {
               "sizeInvalid": {value: sizeControl.value},
            };
            sizeControl.setErrors(invalidSizeError);
            return invalidSizeError;
         }
         // Remove error from size form control to hide the <clr-control-error> element
         sizeControl.setErrors(null);
         return null;
      }
   };
}
