import {ClrDatagridComparatorInterface} from "@clr/angular";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {VsanObject} from "@generated/vsan-object";

export class HealthComparator implements ClrDatagridComparatorInterface<VsanObject> {
   compare = (o1: VsanObject, o2: VsanObject): number => {
      return VsanHealthUtil.getVsanObjectHealthStatusText(o1.healthState).localeCompare(
            VsanHealthUtil.getVsanObjectHealthStatusText(o2.healthState));
   };
}