import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {VsanIscsiTargetMutationProvider} from "@generated/vsan-iscsi-target-mutation-provider";

@Component({
   templateUrl: "remove-target.html",
   selector: "vsan-iscsi-remove-target",
})
export class RemoveTargetComponent implements OnInit {
   private clusterRef: ManagedObject;
   targetAlias: string;

   isRemoving: boolean;
   alertMessages: string[];
   allowRemove: boolean;

   constructor(private iscsiTargetMutationProvider: VsanIscsiTargetMutationProvider) {}

   ngOnInit() {
      this.clusterRef = ManagedObject.contextObject;
      this.targetAlias = VsanUiUtils.getModalContext().targetAlias;
      this.allowRemove = VsanUiUtils.getModalContext().lunCount === 0;
   }

   ok = (): void => {
      this.isRemoving = true;
      this.iscsiTargetMutationProvider.removeTarget(this.clusterRef, this.targetAlias)
            .then(this.close)
            .catch((error: string) => {
               this.isRemoving = false;
               this.alertMessages = [error];
            });
   };

   close = (taskRef: ManagedObject = null): void => {
      this.isRemoving = false;
      VsanUiUtils.closeModalDialog(taskRef);
   };
}