import {IscsiTarget} from "@generated/iscsi-target";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

export class IoOwnerHostFilter implements ClrDatagridStringFilterInterface<IscsiTarget> {
   constructor(private addtionalData: Map<string, string>) {}

   accepts = (target: IscsiTarget, search: string): boolean => {
      return !!this.addtionalData && this.addtionalData.get(target.ioOwnerHost).indexOf(search) > -1;
   };
}