import {IscsiLun} from "@generated/iscsi-lun";
import {IscsiTarget} from "@generated/iscsi-target";
import {InitiatorGroupTarget} from "@generated/initiator-group-target";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

export class AliasFilter implements ClrDatagridStringFilterInterface<IscsiLun | IscsiTarget | InitiatorGroupTarget> {
   accepts = (obj: IscsiLun | IscsiTarget | InitiatorGroupTarget, search: string): boolean => {
      return obj.alias !== null && obj.alias.toLowerCase().indexOf(search) >= 0;
   };
}