import {Component, Input, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {ModalBuilder} from "@util/modal-builder";
import {VsanIscsiInitiatorGroupPropertyProvider} from "@generated/vsan-iscsi-initiator-group-property-provider";
import {InitiatorGroup} from "@generated/initiator-group";
import {NameFilter} from "../filters/name-filter";
import {InitiatorsFilter} from "../filters/initiators-filter";
import {InitiatorsComparator} from "../comparators/initiators-comparator";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";

@Component({
   selector: "vsan-initiator-groups-view",
   templateUrl: "initiator-groups-view.html",
   styleUrls: ["initiator-groups-view.scss", "iscsi-common.scss"],
})
export class InitiatorGroupsViewComponent implements OnInit {
   VsanUiUtils = VsanUiUtils;

   loading: boolean = false;
   alertMessage: string;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   selectedInitiatorGroup: InitiatorGroup;

   private _initiatorGroupList: InitiatorGroup[];

   set initiatorGroupList(list: InitiatorGroup[]) {
      if (!list || list.length === 0) {
         this._initiatorGroupList = [];
         this.selectedInitiatorGroup = null;
      } else {
         this._initiatorGroupList = list;
         this.selectedInitiatorGroup = this.initiatorGroupList[0];
      }
   }

   get initiatorGroupList(): InitiatorGroup[] {
      return this._initiatorGroupList;
   }

   @Input()
   hasEditPermissions: boolean = false;

   nameFilter: NameFilter = new NameFilter();
   initiatorCountFilter: InitiatorsFilter = new InitiatorsFilter();
   initiatorCountComparator: InitiatorsComparator = new InitiatorsComparator();

   constructor(private iscsiInitiatorGroupProvider: VsanIscsiInitiatorGroupPropertyProvider) {}

   ngOnInit() {
      this.fetch();
   }

   fetch() {
      this.refreshGroupList();
   }

   refreshGroupList = () => {
      this.loading = true;
      this.iscsiInitiatorGroupProvider.getVsanIscsiInitiatorGroupList(ManagedObject.contextObject)
            .then((data: InitiatorGroup[]) => {
               this.loading = false;
               this.initiatorGroupList = data;
               this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded.andTabSelected",
                     VsanUiUtils.getString("vsan.iscsiTargets"),
                     VsanUiUtils.getString("vsan.configure.iscsiTargetService.initiator.groups"));
            })
            .catch((error: string) => {
               this.loading = false;
               this.alertMessage =
                     VsanUiUtils.getString("vsan.configure.iscsiTargets.initiatorGroups.list.query.error");
            });
   };

   selectGroup = (group: InitiatorGroup) => {
      if (this.selectedInitiatorGroup === group) {
         return;
      }
      this.selectedInitiatorGroup = group;
   };

   openAddInitiatorGroupDialog = () => {
      const context: any = {
         groupNames: this.getAllInitiatorGroupNames(),
      };
      new ModalBuilder("iscsiTargets/addInitiatorGroup")
            .setSize(650, 600)
            .open(context)
            .then(() => {
               this.refreshGroupList();
            });
   };

   private getAllInitiatorGroupNames = (): string[] => {
      if (!this.initiatorGroupList || this.initiatorGroupList.length === 0) {
         return [];
      }
      return this.initiatorGroupList.map(group => group.name);
   };

   openRemoveInitiatorGroupDialog = () => {
      new ModalBuilder("iscsiTargets/removeInitiatorGroup")
            .setSize(500, 240)
            .open({
               groupName: this.selectedInitiatorGroup.name,
            })
            .then((updateGroupList: boolean) => {
               if (updateGroupList) {
                  this.refreshGroupList();
               }
            });
   };
}