import {Component, EventEmitter, Input, Output} from "@angular/core";
import {ModalBuilder} from "../../common/util/modal-builder";
import {InitiatorGroupTarget} from "@generated/initiator-group-target";
import {IqnFilter} from "../filters/iqn-filter";
import {AliasFilter} from "../filters/alias-filter";
import {VsanUiUtils} from "app/vsan/common/util/vsan-util";

@Component({
   selector: "vsan-accessible-targets",
   templateUrl: "accessible-targets.html",
   styleUrls: ["accessible-targets.scss", "iscsi-common.scss"],
})
export class AccessibleTargetsComponent {

   VsanUiUtils = VsanUiUtils;

   @Output()
   refreshGroupList: EventEmitter<void> = new EventEmitter<void>();

   @Input()
   initiatorGroupName: string;

   @Input()
   set accessibleTargets(targets: InitiatorGroupTarget[]) {
      if (!targets || targets.length === 0) {
         this._accessibleTargets = [];
         this.selectedAccessibleTarget = null;
      } else {
         this._accessibleTargets = targets;
         this.selectedAccessibleTarget = this._accessibleTargets[0];
      }
   }

   get accessibleTargets(): InitiatorGroupTarget[] {
      return this._accessibleTargets;
   }

   private _accessibleTargets: InitiatorGroupTarget[];

   selectedAccessibleTarget: InitiatorGroupTarget;

   @Input()
   hasEditPermissions: boolean = false;

   iqnFilter: IqnFilter = new IqnFilter();
   aliasFilter: AliasFilter = new AliasFilter();

   selectAccessibleTarget = (target: InitiatorGroupTarget): void => {
      if (target === this.selectedAccessibleTarget) {
         return;
      }
      this.selectedAccessibleTarget = target;
   };

   openRemoveAccessibleTargetDialog = (): void => {
      new ModalBuilder("iscsiTargets/removeAccessibleTarget")
            .setSize(500, 220)
            .open({
               groupName: this.initiatorGroupName,
               targetAlias: this.selectedAccessibleTarget.alias,
            })
            .then((updateGroupList: boolean) => {
               if (updateGroupList) {
                  this.refreshGroupList.emit();
               }
            });
   };

   onAddAccessibleTarget = (): void => {
      new ModalBuilder("iscsiTargets/addAccessibleTarget")
            .setSize(720, 410)
            .open({
               initiatorGroupName: this.initiatorGroupName,
               accessibleTargets: this.accessibleTargets,
            })
            .then((listUpdated: boolean): void => {
               if (listUpdated) {
                  this.refreshGroupList.emit();
               }
            });
   };
}