import {Component, Input} from "@angular/core";
import {NetworkIpSetting} from "@generated/network-ip-setting";

@Component({
   selector: "iscsi-network-ip-settings",
   templateUrl: "network-ip-settings.html",
   styleUrls: ["network-ip-settings.scss"],
})
export class IscsiNetworkIpSettingsComponent {
   @Input()
   networkIpSetting: NetworkIpSetting;

   get emptySetting(): boolean {
      return !this.networkIpSetting.ipV4Address && !this.networkIpSetting.ipV6Address;
   }
}