import {InitiatorGroup} from "@generated/initiator-group";
import {TargetInitiatorSpec} from "@generated/target-initiator-spec";
import {InitiatorGroupInitiator} from "@generated/initiator-group-initiator";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

export class NameFilter implements ClrDatagridStringFilterInterface<InitiatorGroup | TargetInitiatorSpec | InitiatorGroupInitiator> {
   accepts = (obj: InitiatorGroup | TargetInitiatorSpec | InitiatorGroupInitiator, search: string): boolean => {
      return obj.name !== null && obj.name.toLowerCase().indexOf(search) >= 0;
   };
}