import {IscsiLun} from "@generated/iscsi-lun";
import {ClrDatagridStringFilterInterface} from "@clr/angular";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";

export class LunStateFilter implements ClrDatagridStringFilterInterface<IscsiLun> {
   accepts = (lun: IscsiLun, search: string): boolean => {
      return VsanIscsiUtil.getLunStateLabel(lun.status).toLowerCase().indexOf(search) > -1;
   };
}