import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {VsanIscsiInitiatorGroupMutationProvider} from "@generated/vsan-iscsi-initiator-group-mutation-provider";

@Component({
   templateUrl: "remove-initiator-group.html",
   selector: "vsan-iscsi-remove-group",
})

export class RemoveInitiatorGroupComponent implements OnInit {
   private groupName: string;
   isRemoving: boolean = false;
   alertMessages: string[];

   constructor (private initiatorGroupMutationProvider: VsanIscsiInitiatorGroupMutationProvider) {}

   ngOnInit() {
      this.groupName = VsanUiUtils.getModalContext().groupName;
   }

   ok = (): void => {
      this.isRemoving = true;
      this.initiatorGroupMutationProvider.removeInitiatorGroup(ManagedObject.contextObject, this.groupName)
            .then(() => {
               this.isRemoving = false;
               VsanUiUtils.closeModalDialog(true);
            })
            .catch((error: string) => {
               this.isRemoving = false;
               this.alertMessages = [error];
            });
   };
}
