import {InitiatorGroup} from "@generated/initiator-group";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

export class InitiatorsFilter implements ClrDatagridStringFilterInterface<InitiatorGroup> {
   accepts = (group: InitiatorGroup, search: string): boolean => {
      return group.initiators.length.toString().indexOf(search) > -1;
   };
}