import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {InitiatorGroupInitiatorRemoveSpec} from '@generated/initiator-group-initiator-remove-spec';
import {VsanIscsiInitiatorGroupMutationProvider} from "@generated/vsan-iscsi-initiator-group-mutation-provider";

@Component({
   templateUrl: "remove-initiator.html",
   selector: "vsan-iscsi-remove-initiators",
})

export class RemoveInitiatorComponent implements OnInit {
   isRemoving: boolean;
   alertMessages: string[];
   groupName: string;
   initiatorName: string;

   constructor (private initiatorGroupMutationProvider: VsanIscsiInitiatorGroupMutationProvider) {}

   ngOnInit() {
      this.groupName = VsanUiUtils.getModalContext().groupName;
      this.initiatorName = VsanUiUtils.getModalContext().initiatorName;
   }

   ok = (): void => {
      let removeSpec: InitiatorGroupInitiatorRemoveSpec = InitiatorGroupInitiatorRemoveSpec.Factory.create(
            this.groupName, this.initiatorName);
      this.isRemoving = true;
      this.initiatorGroupMutationProvider.removeInitiator(ManagedObject.contextObject, removeSpec)
            .then(() => {
               this.isRemoving = false;
               VsanUiUtils.closeModalDialog(true);
            })
            .catch((error: string) => {
               this.isRemoving = false;
               this.alertMessages = [error];
            });
   };
}
