import {ClrDatagridStringFilterInterface} from "@clr/angular";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {VsanObject} from "@generated/vsan-object";

export class HealthFilter implements ClrDatagridStringFilterInterface<VsanObject> {
   accepts = (obj: VsanObject, search: string): boolean => {
      return VsanHealthUtil.getVsanObjectHealthStatusText(obj.healthState).toLowerCase().indexOf(search) > -1;
   };
}