import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";
import {AbstractControl, FormBuilder, FormControl, FormGroup, ValidationErrors, ValidatorFn} from "@angular/forms";
import {ReactiveFormUtil} from "@util/reactive-form-util";

@Component({
   selector: "vsan-add-initiators",
   templateUrl: "add-initiators.html",
   styleUrls: ["../view/iscsi-common.scss", "add-initiators.scss"],
})
export class AddInitiatorsComponent implements OnInit {
   addInitiatorGroup: FormGroup;
   selectedInitiator: string;
   initiators: string[] = [];
   nameErrorMessage: string;

   @Input()
   isRequired: boolean;

   @Input()
   existingInitiators: string[];

   @Output()
   selectedInitiators: EventEmitter<string[]> = new EventEmitter<string[]>();

   constructor(private formBuilder: FormBuilder) {
      this.addInitiatorGroup = this.formBuilder.group({
         initiatorName: new FormControl("", [this.validate])
      });
   }

   ngOnInit() {
      if (this.existingInitiators) {
         // Copy into new array to avoid changing the original array
         this.initiators = this.existingInitiators.map( initiator => initiator);
      }
   }

   validate: ValidatorFn = (ctrl: AbstractControl): ValidationErrors | null => {
         const initiatorName: string = ctrl.value;
         this.nameErrorMessage = null;

         if (this.isRequired && !initiatorName.trim()) {
            this.nameErrorMessage = VsanUiUtils.getString("vsan.configure.iscsiTargets.initiators.name.isNull");
         }
         // Verify that the name matches the rules.
         else if (!VsanIscsiUtil.validateIqn(initiatorName)) {
            return {'incorrectNameRules': {value: initiatorName}};
         }
         // Verify that the name is duplicated.
         else if (this.initiators.length > 0 && this.initiators.includes(initiatorName)) {
            this.nameErrorMessage = VsanUiUtils.getString("vsan.configure.iscsiTargets.initiators.name.exist");
         }
         // Verify that the name doesn't exist in the already added initiators from the initiator group.
         else if(this.existingInitiators && this.existingInitiators.includes(initiatorName)) {
            this.nameErrorMessage = VsanUiUtils.getString("vsan.configure.iscsiTargets.initiators.name.exist");
         }

         if (!this.nameErrorMessage) {
            return null;
         }

         return {'validationError': {value: this.nameErrorMessage}};
   };

   selectInitiator = (initiator: string): void => {
      if (this.selectedInitiator == initiator) {
         return;
      }
      this.selectedInitiator = initiator;
   };

   addInitiator = (): void => {
      ReactiveFormUtil.validateAllFormFields(this.addInitiatorGroup);

      if (!this.addInitiatorGroup.valid) {
         return;
      }

      const control = this.addInitiatorGroup.get("initiatorName");
      this.initiators.push(control.value);
      this.selectedInitiators.emit(this.initiators);
      control.patchValue("");
      this.nameErrorMessage = null;
   };

   addAllowed = (): boolean => {
      if (this.isRequired) {
         // When initiators are required all check are included in the form validation.
         return this.addInitiatorGroup.valid && this.addInitiatorGroup.touched;
      } else {
         // When initiators aren't required this check is not included in the form validation.
         const initiatorNameIsNotEmpty = this.addInitiatorGroup.get("initiatorName").value.trim().length > 0;
         return this.addInitiatorGroup.valid && this.addInitiatorGroup.touched && initiatorNameIsNotEmpty;
      }
   };

   // Only allow remove on initiators added during the dialog, but not ones that existed before that
   removeAllowed = (): boolean => {
      // If no selection, not allowed
      if (!this.selectedInitiator) {
         return false;
      }
      // The are no existing initiators, so nothing more to check
      if (!this.existingInitiators) {
         return true;
      }
      // Finally check that the selected one is not from existing ones
      return !this.existingInitiators.includes(this.selectedInitiator);
   };

   removeInitiator = (): void => {
      this.initiators = this.initiators.filter(existingName => {return existingName != this.selectedInitiator});
      this.selectedInitiators.emit(this.initiators);
      this.selectedInitiator = null;
   };
}
