import {VsanIscsiUtil} from "../../common/util/vsan-iscsi-util";
import {TargetInitiatorSpec} from "@generated/target-initiator-spec";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

export class IsGroupFilter implements ClrDatagridStringFilterInterface<TargetInitiatorSpec> {
   accepts = (spec: TargetInitiatorSpec, search: string): boolean => {
      return VsanIscsiUtil.getAllowedInitiatorsType(spec.isGroup).toLowerCase().indexOf(search) > -1;
   };
}