import {Component, OnInit} from "@angular/core";
import {Icon} from "@util/icon";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {IscsiTarget} from "@generated/iscsi-target";
import {TargetOperationSpec} from "@generated/target-operation-spec";
import {VsanIscsiTargetMutationProvider} from "@generated/vsan-iscsi-target-mutation-provider";

@Component({
   selector: "vsan-batch-reapply",
   templateUrl: "batch-policy-reapply.html",
   styleUrls: ["../view/iscsi-common.scss"],
})
export class BatchPolicyReapplyComponent implements OnInit {
   Icon = Icon;

   reapplying: boolean;
   alertMessages: string[] = [];
   content: string;
   selectedTargets: IscsiTarget[] = [];
   targets: IscsiTarget[] = [];

   constructor(private targetMutationProvider: VsanIscsiTargetMutationProvider) {}

   ngOnInit() {
      this.targets = VsanUiUtils.getModalContext().targets;

      const total: number = this.targets.length;
      if (total === 1) {
         this.content = VsanUiUtils.getString(
               "vsan.configure.iscsiTargets.target.batch.reapply.dialog.content.singular");
      } else {
         this.content = VsanUiUtils.getString(
               "vsan.configure.iscsiTargets.target.batch.reapply.dialog.content.plural", total);
      }
   }

   get applyButtonEnabled(): boolean {
      return !this.reapplying && this.selectedTargets.length > 0;
   }

   ok = (): void => {
      this.alertMessages = [];

      const specs: TargetOperationSpec[] = this.selectedTargets.map((target: IscsiTarget) => {
         return TargetOperationSpec.Factory.create(target.alias, null, null, null, null, null,
               target.vmStoragePolicyUuid, null);
      });

      this.reapplying = true;
      this.targetMutationProvider.batchPolicyReapply(ManagedObject.contextObject, specs).then(this.close)
            .catch((error: string) => {
               this.reapplying = false;
               this.alertMessages.push(error);
            });
   };

   private close = (tasks: ManagedObject[] = null): void => {
      this.reapplying = false;
      VsanUiUtils.closeModalDialog(tasks);
   };
}