import {Component, EventEmitter, Input, Output} from "@angular/core";
import {ModalBuilder} from "../../common/util/modal-builder";
import {InitiatorGroupInitiator} from "@generated/initiator-group-initiator";
import {NameFilter} from "../filters/name-filter";
import {VsanUiUtils} from "../../common/util/vsan-util";

@Component({
   selector: "vsan-initiator-list",
   templateUrl: "initiator-list.html",
   styleUrls: ["initiator-list.scss", "iscsi-common.scss"],
})
export class InitiatorListComponent {

   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;

   @Input()
   initiatorGroupName: string;

   selectedInitiator: InitiatorGroupInitiator;

   private _initiatorList: InitiatorGroupInitiator[];

   @Input()
   set initiatorList(list: InitiatorGroupInitiator[]) {
      if (!list || list.length === 0) {
         this._initiatorList = [];
         this.selectedInitiator = null;
      } else {
         this._initiatorList = list;
         this.selectedInitiator = this._initiatorList[0];
      }
   }

   get initiatorList(): InitiatorGroupInitiator[] {
      return this._initiatorList;
   }

   @Input()
   hasEditPermissions: boolean;

   @Output()
   private refreshGroupList: EventEmitter<void> = new EventEmitter<void>();

   nameFilter: NameFilter = new NameFilter();

   selectInitiator = (initiator: InitiatorGroupInitiator): void => {
      if (initiator === this.selectedInitiator) {
         return;
      }
      this.selectedInitiator = initiator;
   };

   openAddInitiatorsDialog = (): void => {
      const dialogData: any = {
         groupName: this.initiatorGroupName,
         existingInitiators: this.initiatorList,
      };

      new ModalBuilder("iscsiTargets/addInitiators")
            .setSize(580, 425)
            .open(dialogData)
            .then(() => {
               this.refreshGroupList.emit();
            });
   };

   openRemoveInitiatorDialog = (): void => {
      new ModalBuilder("iscsiTargets/removeInitiator")
            .setSize(450, 200)
            .open({
               groupName: this.initiatorGroupName,
               initiatorName: this.selectedInitiator.name,
            })
            .then((updateGroupList: boolean) => {
               if (updateGroupList) {
                  this.refreshGroupList.emit();
               }
            });
   };
}
