import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {InitiatorGroupInitiator} from "@generated/initiator-group-initiator";
import {InitiatorGroupInitiatorAdditionSpec} from '@generated/initiator-group-initiator-addition-spec';
import {VsanIscsiInitiatorGroupMutationProvider} from "@generated/vsan-iscsi-initiator-group-mutation-provider";

@Component({
   selector: "vsan-add-initiators-to-group",
   templateUrl: "add-initiators-to-group.html",
})
export class AddInitiatorsToGroupComponent implements OnInit {

   alertMessages: string[];
   isCreatingInitiators: boolean = false;

   groupName: string;
   existingInitiators: string[];

   private initiatorsToAdd: string[];

   constructor(private initiatorGroupMutationProvider: VsanIscsiInitiatorGroupMutationProvider) {
   }

   ngOnInit(): void {
      this.groupName = VsanUiUtils.getModalContext().groupName;
      const initiatorGroups: InitiatorGroupInitiator[] = VsanUiUtils.getModalContext().existingInitiators || [];
      this.existingInitiators = initiatorGroups.map(initiator => initiator.name);
   }

   updateSelection(value: string[]): void {
      // Only leave new initiators in the updated list for add
      this.initiatorsToAdd = value.filter(initiator =>
            !this.existingInitiators ||
            !this.existingInitiators.includes(initiator));
   }

   ok = (): void => {
      const additionSpec: InitiatorGroupInitiatorAdditionSpec = InitiatorGroupInitiatorAdditionSpec.Factory.create(
            this.groupName, this.initiatorsToAdd);
      this.isCreatingInitiators = true;
      this.initiatorGroupMutationProvider.addInitiators(ManagedObject.contextObject, additionSpec)
            .then(() => {
               this.isCreatingInitiators = false;
               VsanUiUtils.closeModalDialog(true);
            })
            .catch((error: string) => {
               this.isCreatingInitiators = false;
               this.alertMessages = [error];
            });
   };

   get blockFinishButton(): boolean {
      return this.isCreatingInitiators || !this.initiatorsToAdd || this.initiatorsToAdd.length === 0;
   }
}
