import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {IscsiLunStatus} from "@generated/iscsi-lun-status";
import {LunOperationSpec} from "@generated/lun-operation-spec";
import {VsanIscsiTargetMutationProvider} from "@generated/vsan-iscsi-target-mutation-provider";
import {EditLunStateContext} from "@navigation/context/edit-lun-state-context";
import {Logger} from "@util/logger";

@Component({
   templateUrl: "edit-lun-state.html",
   selector: "vsan-iscsi-edit-lun-state",
})
export class EditLunStateComponent implements OnInit {

   title: string;
   content: string;
   inProgress: boolean;
   editSpec: LunOperationSpec;
   alertMessages: string[] = [];

   constructor (private targetMutationProvider: VsanIscsiTargetMutationProvider) {}

   ngOnInit() {
      const context: EditLunStateContext = VsanUiUtils.getModalContext();
      this.editSpec = context.editSpec;
      if (this.editSpec.status === IscsiLunStatus.Online.toString()) {
         this.title = VsanUiUtils.getString("vsan.configure.iscsiTargets.lun.dialog.online.title");
         this.content = VsanUiUtils.getString("vsan.configure.iscsiTargets.lun.dialog.online.content");
      } else {
         this.title = VsanUiUtils.getString("vsan.configure.iscsiTargets.lun.dialog.offline.title");
         this.content = VsanUiUtils.getString("vsan.configure.iscsiTargets.lun.dialog.offline.content");
      }
   }

   async ok() {
      try {
         this.inProgress = true;
         let task: ManagedObject =
               await this.targetMutationProvider.editLun(ManagedObject.contextObject, this.editSpec);
         VsanUiUtils.closeModalDialog({
            tasks: [task],
            refreshTargetList: false,
            aliasOfChangedTarget: this.editSpec.targetAlias,
         });
      } catch (error) {
         Logger.error(error);
         this.alertMessages = [error];
      } finally {
         this.inProgress = false;
      }
   }

   get onlineOperation(): boolean {
      return this.editSpec.status === IscsiLunStatus.Online.toString();
   }
}