import {IscsiLun} from "@generated/iscsi-lun";
import {VsanUiUtils} from "../../common/util/vsan-util";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

export class LunSizeFilter implements ClrDatagridStringFilterInterface<IscsiLun> {
   accepts = (lun: IscsiLun, search: string): boolean => {
      return VsanUiUtils.formatFileSize(lun.lunSize).toLowerCase().indexOf(search) > -1;
   };
}