import {IscsiTarget} from "@generated/iscsi-target";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

export class NetworkInterfaceFilter implements ClrDatagridStringFilterInterface<IscsiTarget> {
   accepts = (target: IscsiTarget, search: string): boolean => {
      return target.networkInterface !== null && target.networkInterface.indexOf(search) > -1;
   };
}