import {IscsiTarget} from "@generated/iscsi-target";
import {VsanIscsiUtil} from "../../common/util/vsan-iscsi-util";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

export class TargetLocationFilter implements ClrDatagridStringFilterInterface<IscsiTarget> {
   constructor(private preferredDomainName: string, private secondaryDomainName: string) {}

   accepts = (target: IscsiTarget, search: string): boolean => {
      return VsanIscsiUtil.getTargetLocationLabel(target.affinityLocation,
            this.preferredDomainName, this.secondaryDomainName).toLowerCase().indexOf(search) >= 0;
   };
}