import {IscsiLun} from "@generated/iscsi-lun";
import {ClrDatagridComparatorInterface} from "@clr/angular";

export class LunStateComparator implements ClrDatagridComparatorInterface<IscsiLun> {
   compare = (l1: IscsiLun, l2: IscsiLun): number => {
      return l1.status.toString().localeCompare(l2.status.toString());
   };
}